import React from "react";
import { connect } from "react-redux";
import {
	addYearsToSelect,
	deleteYearsFromList,
	selectAllYears,
	deleteAllYears,
} from "../../../../Redux/actions";
import { ReactComponent as Arrow } from "../../../../assets/icon/play.svg";
import { ReactComponent as FastArrow } from "../../../../assets/icon/fast-forward.svg";

function YearsArrow(props) {
	return (
		<div
			style={{
				display: "flex",
				flexDirection: "column",
				transform: "translateY(-28px)",
			}}
		>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
					transform: "translateY(-22px)",
				}}
			>
				<Arrow
					onClick={() => {
						props.addYearsToSelect();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(24px)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>

				<Arrow
					onClick={() => {
						props.deleteYearsFromList();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(35px) rotate(180deg)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>
			</div>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
				}}
			>
				<FastArrow
					onClick={() => {
						props.selectAllYears();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(43px) scale(0.8)",
						fill: "#888888",
						width: "14px",
					}}
				/>
				<FastArrow
					onClick={() => {
						props.deleteAllYears();
					}}
					style={{
						cursor: "pointer",
						fill: "#888888",
						transform: "translateY(54px) rotate(180deg) scale(0.8)",
						width: "14px",
					}}
				/>
			</div>
		</div>
	);
}

const mapDispatch = (dispatch) => {
	return {
		selectAllYears: (year) => dispatch(selectAllYears(year)),
		deleteAllYears: (year) => dispatch(deleteAllYears(year)),
		addYearsToSelect: () => dispatch(addYearsToSelect()),
		deleteYearsFromList: () => dispatch(deleteYearsFromList()),
	};
};

export default connect(null, mapDispatch)(YearsArrow);
