/** @format */

const initialState = {
	tableData: "",
	tableCSV: [],
	tableIsLoading: false,
};

export default function(state = initialState, action) {
	switch (action.type) {
		case "ADD_TABEL_DATA": {
			return {
				...state,
				tableData: action.date,
				tableIsLoading: false,
			};
		}
		case "ADD_CSV": {
			return {
				...state,
				tableCSV: [...new Set(action.csv)],
			};
		}
		case "ADD_LOADER": {
			return {
				...state,
				tableIsLoading: true,
			};
		}
		case "RESET_TABLE": {
			return {
				...state,
				tableData: "",
				tableCSV: [],
			};
		}
		default:
			return {
				...state,
			};
	}
}
