/** @format */

import React from "react";
import { Droppable, Draggable } from "react-beautiful-dnd";
import DayItem from "./DayItem";

export default function DaySelected(props) {
	return (
		<div
			className="modal-body-select"
			style={{
				minHeight: "200px",
			}}
		>
			<div
				className="modal-filter modal-filter-select"
				style={{
					marginTop: "0",
					minHeight: "200px",
				}}
			>
				{props.itemList.length != 0 ? (
					props.itemList.map((item, index) => {
						return (
							<div key={item}>
								<DayItem colname="selected" item={item} />
							</div>
						);
					})
				) : (
					<p
						style={{
							color: "#757474",
						}}
					></p>
				)}
			</div>
		</div>
	);
}
