/** @format */

import React from "react";
import { ReactComponent as Arrow } from "../../../assets/icon/play.svg";
import { ReactComponent as FastArrow } from "../../../assets/icon/fast-forward.svg";
import { connect } from "react-redux";
import {
	superOk,
	addBranchesToList,
	deleteBranchesFromList,
	addBranches,
	addBranchToList,
	deleteBranchFromList,
	closeBranchModal,
	resetBranches,
	selectAll,
	undoBranches,
	clearBranchHistory,
	cancelBranches,
	saveBranches,
} from "../../../Redux/actions";
import { withStyles } from "@material-ui/core/styles";
import Selected from "./Selected";
import Select from "./Select";
import { Modal, Button } from "react-bootstrap";

const styles = (theme) => ({
	input: {
		height: 32,
	},
	labelRoot: {
		fontSize: 14,
		transform: "translate(14px, 10px) scale(1)",
	},
});

const DragModal = withStyles(styles)((props) => {
	const prevDef = (event) => {
		event.preventDefault();
	};

	const { classes, fetchCols, shadowCols } = props;

	var colLeft = props.branchesCol.selectBranches;
	var colRighr = props.branchesCol.selectedBranches;

	return (
		<Modal
			show={props.showModalBranch}
			onHide={props.closeBranchModal}
			backdrop="static"
			onExit={() => {
				if (props.undoState.length > 0 || props.undoStated.length > 0) {
					props.clearBranchHistory();
				}
			}}
			centered
		>
			<Modal.Header closeButton>
				<span>Branches</span>
			</Modal.Header>
			<Modal.Body p={5}>
				<Select classes={classes} branches={props.branches} colLeft={colLeft} />
				<div
					style={{
						display: "flex",
						flexDirection: "column",
						transform: "translateY(-5px)",
					}}
				>
					<div
						style={{
							display: "flex",
							flexDirection: "column",
							justifyContent: "center",
							alignItems: "center",
							transform: "translateY(-22px)",
						}}
					>
						<Arrow
							onClick={() => {
								props.addBranchesToList();
							}}
							style={{
								cursor: "pointer",
								transform: "translateY(24px)  scale(0.8)",
								width: "14px",
								fill: "#888888",
							}}
						/>

						<Arrow
							onClick={() => {
								props.deleteBranchesFromList();
							}}
							style={{
								cursor: "pointer",
								transform: "translateY(35px) rotate(180deg)  scale(0.8)",
								width: "14px",
								fill: "#888888",
							}}
						/>
					</div>
					<div
						style={{
							display: "flex",
							flexDirection: "column",
							justifyContent: "center",
							alignItems: "center",
						}}
					>
						<FastArrow
							onClick={() => {
								props.selectAll();
							}}
							style={{
								cursor: "pointer",
								transform: "translateY(43px) scale(0.8)",
								fill: "#888888",
								width: "14px",
							}}
						/>
						<FastArrow
							onClick={() => {
								props.resetBranches();
							}}
							style={{
								cursor: "pointer",
								fill: "#888888",
								transform: "translateY(54px) rotate(180deg) scale(0.8)",
								width: "14px",
							}}
						/>
					</div>
				</div>

				<Selected
					classes={classes}
					branches={props.branches}
					colRighr={colRighr}
				/>
			</Modal.Body>
			<Modal.Footer>
				<Button
					variant="secondary"
					onClick={() => {
						if (props.undoState.length > 0 || props.undoStated.length > 0) {
							props.undoBranches();
						}
					}}
					size="sm"
				>
					Undo
				</Button>
				<div>
					<Button
						className="mr-2"
						variant="danger"
						size="sm"
						onClick={() => {
							props.cancelBranches();
							props.closeBranchModal();
						}}
					>
						Cancel
					</Button>
					<Button
						variant="success"
						onClick={() => {
							props.saveBranches();
							props.superOk();
							props.closeBranchModal();
						}}
						size="sm"
						disabled={JSON.stringify(fetchCols) === JSON.stringify(shadowCols)}
					>
						Save Changes
					</Button>
				</div>
			</Modal.Footer>
		</Modal>
	);
});

const mapStore = (store) => {
	return {
		showModalBranch: store.branchesReducer.showModalBranch,
		branchesCol: store.branchesReducer.branchCols,
		branches: store.branchesReducer,
		undoState: store.branchesReducer.oldCols.selectBranches,
		undoStated: store.branchesReducer.oldCols.selectedBranches,
		shadowCols: store.branchesReducer.shadowCols,
		fetchCols: store.branchesReducer.fetchCols,
	};
};

const mapDispatch = (dispatch) => {
	return {
		addBranchesToList: () => dispatch(addBranchesToList()),
		deleteBranchesFromList: () => dispatch(deleteBranchesFromList()),
		superOk: () => dispatch(superOk()),
		undoBranches: () => dispatch(undoBranches()),
		selectAll: () => dispatch(selectAll()),
		resetBranches: () => dispatch(resetBranches()),
		addBranches: (branches) => dispatch(addBranches(branches)),
		addBranchToList: (index, destIndex) =>
			dispatch(addBranchToList(index, destIndex)),
		deleteBranchFromList: (index, destIndex) =>
			dispatch(deleteBranchFromList(index, destIndex)),
		closeBranchModal: () => dispatch(closeBranchModal()),
		clearBranchHistory: () => dispatch(clearBranchHistory()),
		cancelBranches: () => dispatch(cancelBranches()),
		saveBranches: () => dispatch(saveBranches()),
	};
};

export default connect(mapStore, mapDispatch)(DragModal);
