/** @format */

import React from "react";
import { addSelectTemperarly } from "../../../Redux/actions";
import ModalBranchFilteritem from "./ModalBranchFilteritem";
import { connect } from "react-redux";

function BranchColumnItem(props) {
	return (
		<div
			className="filtder"
			style={{
				minHeight: "180px",
			}}
		>
			{props.itemList.length != 0 ? (
				props.itemList.map((item, index) => {
					return (
						<div key={item}>
							<ModalBranchFilteritem
								onDoubleClick={() => {}}
								item={item}
								name={props.name}
							/>
						</div>
					);
				})
			) : (
				<p
					style={{
						color: "#757474",
					}}
				></p>
			)}
		</div>
	);
}

const mapDispatch = (dispatch) => {
	return {
		addSelectTemperarly: (id) => dispatch(addSelectTemperarly(id)),
	};
};

export default connect(null, mapDispatch)(BranchColumnItem);
