import store from "../Redux/store";
const countMask = ["first", "second", "third", "fourth", "fifth"];

function setFetchBody(dispatch, getState) {
	let fetchBody = {};
	const filteredCol = store.getState().filterReducer.columns.filtered_col;
	filteredCol.filItems.forEach((item, index) => {
		if (
			item != "total_free_time" ||
			item != "count" ||
			item != "max_free_time" ||
			item != "total_session_time"
		) {
			let name = item;
			if (item == "branches") {
				name = "branchName";
			} else if (item == "users") {
				name = "fullname";
			}
			console.log("setFetchBody -> index", index);
			console.log("setFetchBody -> countMask[index]", countMask[index]);
			fetchBody[countMask[index]] = {
				[`${name}`]: true,
			};
		}
	});
	filteredCol.filItems.forEach((item) => {
		if (item == "total_free_time") {
			fetchBody["totalFreeTime"] = true;
		} else if (item == "max_free_time") {
			fetchBody["maxFreeTime"] = true;
		} else if (item == "total_session_time") {
			fetchBody["totalSessionTime"] = true;
		} else if (item == "count") {
			fetchBody["freeCount"] = true;
		}
	});
	const {
		allBranchs,
		fetchCols: { selectedBranches },
	} = store.getState().branchesReducer;
	fetchBody.branchIds = [];
	selectedBranches.forEach((item) => {
		fetchBody.branchIds.push(allBranchs[item].id);
	});
	const {
		allUsers,
		usersCols: { selectedUsers },
	} = store.getState().usersReducer;
	fetchBody.userIds = [];
	selectedUsers.forEach((item) => {
		fetchBody.userIds.push(allUsers[item].id);
	});

	const { fetchSelectedDays } = store.getState().pickerReducer;
	const { fetchSelectedYears } = store.getState().yearReducer;
	const { fetchSelectedMonth } = store.getState().monthReducer;
	fetchBody.years = fetchSelectedYears.map((item) => item);
	fetchBody.months = fetchSelectedMonth.map((item) => item);
	fetchBody.days = fetchSelectedDays.map((item) => item);
	return fetchBody;
}

export { setFetchBody };
