/** @format */

import React from "react";
import { connect } from "react-redux";
import SelectYear from "./SelectYear";
import SelectedYear from "./SelectedYear";
import {
	addYearToSelect,
	deleteYearToSelect,
	selectAllYears,
	deleteAllYears,
} from "../../../../Redux/actions";
import YearsArrow from "./YearsArrow";

function Year(props) {
	var colLeft = props.selectYears;
	var colRighr = props.selectedYears;

	return (
		<div className="modal-body">
			<SelectYear branches={props.branches} list={colLeft} itemList={colLeft} />
			<YearsArrow />
			<SelectedYear
				branches={props.branches}
				list={colRighr}
				itemList={colRighr}
			/>
		</div>
	);
}

const mapState = (state) => {
	return {
		selectYears: state.yearReducer.selectYears,
		selectedYears: state.yearReducer.selectedYears,
	};
};

const mapDispatch = (dispatch) => {
	return {
		selectAllYears: (year) => dispatch(selectAllYears(year)),
		deleteAllYears: (year) => dispatch(deleteAllYears(year)),
		addYearToSelect: (year) => dispatch(addYearToSelect(year)),
		deleteYearToSelect: (year) => dispatch(deleteYearToSelect(year)),
	};
};

export default connect(mapState, mapDispatch)(Year);
