/** @format */

import React from "react";
import { connect } from "react-redux";
import SelectMonth from "./SelectMonth";
import SelectedMonth from "./SelectedMonth";
import {
	addMonthToSelect,
	deleteMonthToSelect,
	selectAllMonth,
	deleteAllMonth,
} from "../../../../Redux/actions";
import MonthsArrow from "./MonthsArrow";

const monthArr = [
	"January",
	"February",
	"March",
	"April",
	"May",
	"June",
	"July",
	"August",
	"September",
	"October",
	"November",
	"December",
];

function Month(props) {
	var colLeft = props.selectMonth;
	var colRighr = props.selectedMonth;

	return (
		<div className="modal-body">
			<SelectMonth
				branches={props.branches}
				list={colLeft}
				itemList={colLeft}
				months={monthArr}
			/>
			<MonthsArrow />
			<SelectedMonth
				branches={props.branches}
				list={colRighr}
				itemList={colRighr}
				months={monthArr}
			/>
		</div>
	);
}

const mapState = (state) => {
	return {
		selectMonth: state.monthReducer.selectMonth,
		selectedMonth: state.monthReducer.selectedMonth,
	};
};

const mapDispatch = (dispatch) => {
	return {
		selectAllMonth: () => dispatch(selectAllMonth()),
		deleteAllMonth: () => dispatch(deleteAllMonth()),
		addMonthToSelect: (month) => dispatch(addMonthToSelect(month)),
		deleteMonthToSelect: (month) => dispatch(deleteMonthToSelect(month)),
	};
};

export default connect(mapState, mapDispatch)(Month);
