import React, { useEffect, useState } from "react";
import Header from "./Header";
import Container from "./Container";
import List from "./List";
import { BASE_URL } from "../../utils/constants";
import fetchReports from "../../utils/fetchReports";

export default function ReportList() {
	const [reports, setReports] = useState([]);
	const [open, setOpen] = React.useState(false);
	const [openError, setOpenError] = useState(false);
	const handleClose = (event, reason) => {
		if (reason === "clickaway") {
			return;
		}
		setOpen(false);
	};
	const handleCloseError = (event, reason) => {
		if (reason === "clickaway") {
			return;
		}
		setOpenError(false);
	};
	const handleOpen = () => {
		setOpen(true);
	};

	const handleOpenError = () => {
		setOpenError(true);
	};

	useEffect(() => {
		fetchReports().then((data) => setReports(data));
	}, [open]);
	const handleSetEditable = (id) => {
		let editableList = reports.map((item) => ({
			...item,
			isEditable: item.id === id ? !item.isEditable : false,
		}));
		setReports(editableList);
	};
	return (
		<div
			className="content_wrapper d-flex flex-column"
			style={{
				padding: "0",
			}}
		>
			<Header />
			<Container>
				<List
					reports={reports}
					handleSetEditable={handleSetEditable}
					handleClose={handleClose}
					handleOpen={handleOpen}
					open={open}
					openError={openError}
					handleOpenError={handleOpenError}
					handleCloseError={handleCloseError}
				/>
			</Container>
		</div>
	);
}
