/** @format */

import React from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import {
	addDayToList,
	deleteDayFromList,
	addDaySelectTemperarly,
	addDaySelectedTemperarly,
} from "../../../../Redux/actions";

const FilterElement = styled.div`
	padding: 0 5px;
	display: flex;
	font-size: 13px;
	line-height: 14px;
	box-shadow: "0 0 10px #333";
	cursor: pointer;
	background-color: ${(props) => {
		if (props.colname == "select") {
			return props.selectDays.includes(props.item) ? "#4b7ab1" : "";
		} else {
			return props.selectedDays.includes(props.item) ? "#4b7ab1" : "";
		}
	}};
	color: ${(props) => {
		if (props.colname == "selected") {
			return props.selectDays.includes(props.item) ? "#fff" : "333";
		} else {
			return props.selectedDays.includes(props.item) ? "#fff" : "333";
		}
	}};
	&:hover {
		background-color: #4b7ab1;
	}
`;

class DayItem extends React.Component {
	constructor(props) {
		super(props);
		this.clickCount = 0;
		this.singleClickTimer = "";
	}
	singleClick = (keyShift, keyCntrl) => {
		console.log("single click");
		if (this.props.colname == "select") {
			if (keyShift) {
				this.props.addDaySelectTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addDaySelectTemperarly(this.props.item, false, true);
			} else {
				this.props.addDaySelectTemperarly(this.props.item, false, false);
			}
		} else {
			if (keyShift) {
				this.props.addDaySelectedTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addDaySelectedTemperarly(this.props.item, false, true);
			} else {
				this.props.addDaySelectedTemperarly(this.props.item, false, false);
			}
		}
	};
	handleDoubleClick = () => {
		if (this.props.colname == "select") {
			this.props.addDayToList(this.props.item);
		} else {
			this.props.deleteDayFromList(this.props.item);
		}
	};
	handleClicks(keyShift, keyCntrl) {
		this.clickCount++;
		if (this.clickCount === 1) {
			this.singleClickTimer = setTimeout(
				function() {
					this.clickCount = 0;
					this.singleClick(keyShift, keyCntrl);
				}.bind(this),
				300,
			);
		} else if (this.clickCount === 2) {
			clearTimeout(this.singleClickTimer);
			this.clickCount = 0;
			this.handleDoubleClick();
		}
	}
	render() {
		return (
			<FilterElement
				{...this.props}
				onClick={(e) => {
					if (navigator.appVersion.indexOf("Win") != -1) {
						this.handleClicks(e.shiftKey, e.ctrlKey);
					} else {
						this.handleClicks(e.shiftKey, e.metaKey);
					}
				}}
			>
				<p>{this.props.item}</p>
			</FilterElement>
		);
	}
}

const mapState = (state) => {
	return {
		selectDays: state.pickerReducer.temperarlyDays.selectDays,
		selectedDays: state.pickerReducer.temperarlyDays.selectedDays,
	};
};

const mapDispath = (dispatch) => {
	return {
		addDayToList: (day) => dispatch(addDayToList(day)),
		deleteDayFromList: (day) => dispatch(deleteDayFromList(day)),
		addDaySelectTemperarly: (name, isShift, isCntrl) =>
			dispatch(addDaySelectTemperarly(name, isShift, isCntrl)),
		addDaySelectedTemperarly: (name, isShift, isCntrl) =>
			dispatch(addDaySelectedTemperarly(name, isShift, isCntrl)),
	};
};

export default connect(mapState, mapDispath)(DayItem);
