import React from "react";
import { Button } from "react-bootstrap";
import { connect } from "react-redux";
import { newReport } from "../../Redux/actions/";
function ReportListHeader({ newReport }) {
	return (
		<div className="content-head">
			<div
				style={{
					width: "100%",
					padding: "10px",
					background: "#fff",
				}}
				className="d-flex justify-content-between align-items-center pt-3 pb-3 pl-4 pr-4"
			>
				<h1>Reports</h1>
				<Button variant="success" onClick={newReport}>
					New report
				</Button>
			</div>
		</div>
	);
}

const mapDispatch = (dispatch) => {
	return {
		newReport: () => dispatch(newReport()),
	};
};

export default connect(null, mapDispatch)(ReportListHeader);
