import React from "react";
import { connect } from "react-redux";
import { ReactComponent as Arrow } from "../../../../assets/icon/play.svg";
import { ReactComponent as FastArrow } from "../../../../assets/icon/fast-forward.svg";

import {
	addMonthsToSelect,
	deleteMonthsFromList,
	selectAllMonth,
	deleteAllMonth,
} from "../../../../Redux/actions";

function MonthsArrow(props) {
	return (
		<div
			style={{
				display: "flex",
				flexDirection: "column",
				transform: "translateY(-28px)",
			}}
		>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
					transform: "translateY(-22px)",
				}}
			>
				<Arrow
					onClick={() => {
						props.addMonthsToSelect();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(24px)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>

				<Arrow
					onClick={() => {
						props.deleteMonthsFromList();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(35px) rotate(180deg)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>
			</div>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
				}}
			>
				<FastArrow
					onClick={() => {
						props.selectAllMonth();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(43px) scale(0.8)",
						fill: "#888888",
						width: "14px",
					}}
				/>
				<FastArrow
					onClick={() => {
						props.deleteAllMonth();
					}}
					style={{
						cursor: "pointer",
						fill: "#888888",
						transform: "translateY(54px) rotate(180deg) scale(0.8)",
						width: "14px",
					}}
				/>
			</div>
		</div>
	);
}

const mapDispatch = (dispatch) => {
	return {
		selectAllMonth: () => dispatch(selectAllMonth()),
		deleteAllMonth: () => dispatch(deleteAllMonth()),
		addMonthsToSelect: (month) => dispatch(addMonthsToSelect(month)),
		deleteMonthsFromList: (month) => dispatch(deleteMonthsFromList(month)),
	};
};

export default connect(null, mapDispatch)(MonthsArrow);
