/** @format */

import React from "react";
import MonthItem from "./MonthItem";

export default function SelectedMonth(props) {
	return (
		<div className="modal-body-select">
			<div
				className="modal-filter modal-filter-select"
				style={{
					marginTop: "0",
					minHeight: "200px",
				}}
			>
				{props.itemList.length != 0 ? (
					props.itemList.map((item, index) => {
						return (
							<div key={item}>
								<MonthItem colname="selected" item={props.months[item - 1]} />
							</div>
						);
					})
				) : (
					<p></p>
				)}
			</div>
		</div>
	);
}
