import { BASE_URL } from "./constants";
const fetchReports = async () => {
	try {
		const reponse = await fetch(BASE_URL + "getReportList");
		let data = await reponse.json();
		data = data.map((item) => ({
			...item,
			isEditable: false,
		}));
		console.log("fetchReports -> data", data);
		return data;
	} catch (err) {
		console.log(err);
	}
};

export default fetchReports;
