/** @format */

import React from "react";
import { Droppable, Draggable } from "react-beautiful-dnd";
import FilterItem from "./FilterItem";
import { connect } from "react-redux";

function FilterColumn(props) {
	const id = props.colId == 1 ? "setting-table" : "select";
	return (
		<div id={id}>
			<h2 className="filter-title">
				{props.name == "Filtered" ? "Selected models" : "Available models"}
			</h2>

			<Droppable droppableId={props.colId} type="PERSON">
				{(provided, snapshot) => (
					<div
						className="filter"
						style={{
							width: "105%",
							padding: "5px",
							borderRadius: "8px",
							transition: ".3s",
							background: snapshot.isDraggingOver ? "#cec9c9" : "#e7e7e7",
						}}
						ref={provided.innerRef}
						{...provided.droppableProps}
					>
						{props.filterItems.length > 0 ? (
							props.filterItems.map((item, index) => {
								return (
									<div className={`filter-item-wrap ${props.name}`} key={index}>
										<Draggable
											draggableId={index + props.colId + "S"}
											index={index}
										>
											{(provided, snapshot) => (
												<FilterItem
													INNDRAGGUBLE={provided.draggableProps}
													INNREF={provided.innerRef}
													HUNDLER={provided.dragHandleProps}
													name={item.name}
													widthConf={item.widthparam}
													colName={props.name}
													elemIndex={index}
												/>
											)}
										</Draggable>
									</div>
								);
							})
						) : props.name == "Filtered" ? (
							<p style={{ color: "#333", fontWeight: "700" }}>
								Add models here
							</p>
						) : (
							<p style={{ color: "#333", fontWeight: "700" }}>
								All models selected
							</p>
						)}

						{provided.placeholder}
					</div>
				)}
			</Droppable>
		</div>
	);
}

export default connect()(FilterColumn);
