import React, { useState, useEffect } from "react";
import $ from "jquery";
import About from "../../assets/icon/about-1.jpg";
import "./sidebar.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faMap,
	faExchangeAlt,
	faLaptop,
	faThLarge,
	faUser,
	faChartBar,
	faUniversity,
} from "@fortawesome/free-solid-svg-icons";
import DepartmentsModal from "../Modals/Departments/DepartmentsModal";
import CountersModal from "../Modals/CountersModal/CountersModal";

export default function SideBar() {
	const [showDepartments, setDepartmentsModal] = useState(false);
	const [showCounters, setCountersModal] = useState(false);

	const handlToggleDepartments = (e) => {
		console.log("handlToggleDepartments -> e", e);
		if (e !== undefined) {
			e.preventDefault();
		}
		setDepartmentsModal(!showDepartments);
	};

	const handlToggleCounters = (e) => {
		if (e !== undefined) {
			e.preventDefault();
		}
		setCountersModal(!showCounters);
	};

	useEffect(() => {
		$.ajax({
			url: "/rest/servicepoint/user/",
			type: "GET",
			success: function(data) {
				$("#usrnm").text(data["fullName"]);
				$(".img-circle").attr(
					"src",
					"/heatmap/assets/images/profile/" + data["userName"] + ".jpg",
				);
			},
		});
	}, []);

	return (
		<div className="side_bar scroll_auto">
			<div className="user-panel">
				<div className="user_image">
					<img src={About} className="img-circle mCS_img_loaded" alt="User" />
				</div>

				<div className="info">
					<p id="usrnm">Alexander Pierce</p>
				</div>
			</div>
			<ul id="dc_accordion" className="sidebar-menu tree">
				<li className="menu_sub">
					<a href="/heatmap">
						<FontAwesomeIcon icon={faMap} /> <span>Map</span>
					</a>
				</li>

				<li className="menu_sub">
					<a href="/heatmap/exchange">
						<FontAwesomeIcon icon={faExchangeAlt} /> <span>Exchange</span>
					</a>
				</li>

				<li className="menu_sub">
					<a href="/heatmap/branches">
						<FontAwesomeIcon icon={faUniversity} /> <span>Branches</span>
					</a>
				</li>

				<li className="menu_sub">
					<a
						type="button"
						href="/heatmap/counters"
						data-toggle="modal"
						data-target="#countermodal"
						data-whatever="@mdo"
						onClick={handlToggleCounters}
					>
						<FontAwesomeIcon icon={faLaptop} />
						<span>Counters</span>
					</a>
				</li>

				<li className="menu_sub">
					<a
						href="/heatmap/departments"
						data-toggle="modal"
						data-target="#departmentmodal"
						data-whatever="@mdo"
						onClick={handlToggleDepartments}
					>
						<FontAwesomeIcon icon={faThLarge} /> <span>Departments </span>
					</a>
				</li>

				<li className="menu_sub">
					<a href="/heatmap/fte">
						<FontAwesomeIcon icon={faUser} /> <span>FTE settings </span>
					</a>
				</li>

				<li className="menu_sub">
					<a
						href="/heatmap/reports"
						style={{
							background: "#387eb4",
						}}
					>
						<FontAwesomeIcon icon={faChartBar} /> <span>Free time reports</span>
					</a>
				</li>
			</ul>
			<DepartmentsModal
				show={showDepartments}
				handlToggleDepartments={handlToggleDepartments}
			/>
			<CountersModal
				show={showCounters}
				handlToggleCounters={handlToggleCounters}
			/>
		</div>
	);
}
