/** @format */

import {} from "../actions/actionTypes";

const initialState = {
	showModalUser: false,
	allUsers: {},
	usersCols: {
		selectUsers: [],
		selectedUsers: [],
	},
	usersIds: [],
	selectSearchText: "",
	selectedSearchText: "",
	oldCols: {
		selectUsers: [],
		selectedUsers: [],
	},
	temperarlyUsers: {
		selectUser: [],
		selectedUser: [],
	},
	shadowCols: {
		selectUser: [],
		selectedUser: [],
	},
	fetchCols: {
		selectUsers: [],
		selectedUsers: [],
	},
};

export default function(state = initialState, action) {
	switch (action.type) {
		case "ADD_EDITOR_USERS": {
			let filteredElements = Object.values(state.allUsers)
				.filter((item) => action.users.includes(item.id))
				.map((item) => item.name);
			console.log("filteredElements", filteredElements);
			let filterElements = state.usersCols.selectUsers.filter(
				(item) => !filteredElements.includes(item),
			);
			console.log("filterElements", filterElements);
			return {
				...state,
				usersCols: {
					selectUsers: [...filterElements],
					selectedUsers: [...filteredElements],
				},
				fetchCols: {
					selectUsers: [...filterElements],
					selectedUsers: [...filteredElements],
				},
			};
		}
		case "ADD_USERS": {
			let USERS = {};
			action.users.forEach((item) => {
				USERS[item.name] = {
					id: item.id,
					name: item.name,
				};
			});
			let selectUsers = action.users.map((item) => {
				return item.name;
			});
			return {
				...state,
				allUsers: USERS,
				usersCols: {
					selectUsers: [...new Set(selectUsers)],
					selectedUsers: [],
				},
			};
		}
		case "ADD_USER_TOLIS": {
			let name = action.userId;
			let oldUserSelectArr = [...state.usersCols.selectUsers];
			let index = oldUserSelectArr.indexOf(name);

			let curelement = oldUserSelectArr.splice(index, 1);
			let oldUserArr = [...state.usersCols.selectedUsers];
			oldUserArr.splice(action.destination, 0, curelement[0]);
			return {
				...state,
				usersCols: {
					selectUsers: oldUserSelectArr,
					selectedUsers: oldUserArr,
				},
				oldCols: {
					selectUsers: [...state.usersCols.selectUsers],
					selectedUsers: [...state.usersCols.selectedUsers],
				},
			};
		}
		case "DELETE_USER_FROMLIST": {
			let name = action.userId;
			let oldUserSelectArr = [...state.usersCols.selectUsers];
			let oldUserArr = [...state.usersCols.selectedUsers];
			let index = oldUserArr.indexOf(name);
			let curelement = oldUserArr.splice(index, 1);

			oldUserSelectArr.splice(action.destination, 0, curelement[0]);
			return {
				...state,
				usersCols: {
					selectUsers: oldUserSelectArr,
					selectedUsers: oldUserArr,
				},
				oldCols: {
					selectUsers: [...state.usersCols.selectUsers],
					selectedUsers: [...state.usersCols.selectedUsers],
				},
			};
		}
		case "ADD_USERS_TOLIST": {
			const users = state.temperarlyUsers.selectUser;
			let oldSelectBranches = [...state.usersCols.selectUsers],
				oldSelectedbranches = [...state.usersCols.selectedUsers];
			users.forEach((item) => {
				let index = oldSelectBranches.indexOf(item);
				oldSelectBranches.splice(index, 1);
				oldSelectedbranches.push(item);
			});
			return {
				...state,
				usersCols: {
					selectUsers: oldSelectBranches,
					selectedUsers: oldSelectedbranches,
				},
				temperarlyUsers: {
					...state.temperarlyUsers,
					selectUser: [],
				},
				oldCols: {
					selectUsers: [...state.usersCols.selectUsers],
					selectedUsers: [...state.usersCols.selectedUsers],
				},
			};
		}
		case "DELETE_USERS_FROMLIST": {
			const users = state.temperarlyUsers.selectedUser;

			let oldSelectUsers = [...state.usersCols.selectUsers],
				oldSelectedUsers = [...state.usersCols.selectedUsers];
			users.forEach((item) => {
				let index = oldSelectedUsers.indexOf(item);
				oldSelectedUsers.splice(index, 1);
				oldSelectUsers.push(item);
			});
			return {
				...state,
				usersCols: {
					selectUsers: oldSelectUsers,
					selectedUsers: oldSelectedUsers,
				},
				temperarlyUsers: {
					...state.temperarlyUsers,
					selectedUser: [],
				},
				oldCols: {
					selectUsers: [...state.usersCols.selectUsers],
					selectedUsers: [...state.usersCols.selectedUsers],
				},
			};
		}
		case "OPEN_USER_MODAL": {
			return {
				...state,
				showModalUser: true,
				shadowCols: {
					selectUsers: [...state.usersCols.selectUsers],
					selectedUsers: [...state.usersCols.selectedUsers],
				},
			};
		}
		case "CLOSE_USER_MODAL": {
			return {
				...state,
				showModalUser: false,
			};
		}
		case "USER_SELECT_SEARCH": {
			return {
				...state,
				selectSearchText: action.text,
			};
		}
		case "USER_SELECTED_SEARCH": {
			return {
				...state,
				selectedSearchText: action.text,
			};
		}
		case "RESET_USERS": {
			const selectedUsers = [...state.usersCols.selectedUsers];
			const selectUsers = [...state.usersCols.selectUsers];
			if (selectedUsers.length == 0) {
				return {
					...state,
				};
			}
			return {
				...state,
				usersCols: {
					selectUsers: [...selectedUsers, ...selectUsers],
					selectedUsers: [],
				},
				oldCols: {
					selectUsers: [...state.usersCols.selectUsers],
					selectedUsers: [...state.usersCols.selectedUsers],
				},
				temperarlyUsers: {
					selectUser: [],
					selectedUser: [],
				},
			};
		}
		case "SELECT_ALL_USERS": {
			const selectedUsers = [...state.usersCols.selectedUsers];
			const selectUsers = [...state.usersCols.selectUsers];
			if (selectUsers.length == 0) {
				return {
					...state,
				};
			}
			return {
				...state,
				usersCols: {
					selectUsers: [],
					selectedUsers: [...selectedUsers, ...selectUsers],
				},
				oldCols: {
					selectUsers: [...state.usersCols.selectUsers],
					selectedUsers: [...state.usersCols.selectedUsers],
				},
				temperarlyUsers: {
					selectUser: [],
					selectedUser: [],
				},
			};
		}
		case "UNDO_USERS": {
			return {
				...state,
				usersCols: {
					selectUsers: [...state.oldCols.selectUsers],
					selectedUsers: [...state.oldCols.selectedUsers],
				},
				oldCols: {
					selectUsers: [],
					selectUsers: [],
				},
			};
		}
		case "USER_OK": {
			return {
				...state,
				oldCols: {
					selectUsers: [],
					selectedUsers: [],
				},
			};
		}
		case "ADD_SELECT_USER_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			console.log("isCntrl", isCntrl);
			let isHas = state.temperarlyUsers.selectUser.includes(name);

			let newArr = [...state.temperarlyUsers.selectUser];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}
			return {
				...state,
				temperarlyUsers: {
					...state.temperarlyUsers,
					selectUser: newArr,
				},
			};
		}
		case "ADD_SELECTED_USER_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			console.log("isCntrl", isCntrl);
			let isHas = state.temperarlyUsers.selectedUser.includes(name);
			let newArr = [...state.temperarlyUsers.selectedUser];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}

			return {
				...state,
				temperarlyUsers: {
					...state.temperarlyUsers,
					selectedUser: newArr,
				},
			};
		}
		case "CLEAR_USER_HISTORY": {
			return {
				...state,
				oldCols: {
					selectUsers: [],
					selectedUsers: [],
				},
				selectSearchText: "",
				selectedSearchText: "",
			};
		}
		case "CANCEL_USER": {
			return {
				...state,
				usersCols: {
					selectUsers: [...state.shadowCols.selectUsers],
					selectedUsers: [...state.shadowCols.selectedUsers],
				},
			};
		}
		case "SAVE_USERS": {
			return {
				...state,
				fetchCols: {
					...state.usersCols,
				},
			};
		}
		default:
			return {
				...state,
			};
	}
}
