/** @format */

import React from "react";
import { connect } from "react-redux";
import { ReactComponent as Arrow } from "../../../../assets/icon/play.svg";
import { ReactComponent as FastArrow } from "../../../../assets/icon/fast-forward.svg";
import {
	addDaysToSelect,
	deleteDaysFromList,
	selectAllDays,
	deleteAllDays,
} from "../../../../Redux/actions";

function DaArrow(props) {
	return (
		<div
			style={{
				display: "flex",
				flexDirection: "column",
				transform: "translateY(-28px)",
			}}
		>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
					transform: "translateY(-22px)",
				}}
			>
				<Arrow
					onClick={() => {
						props.addDaysToSelect();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(24px)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>

				<Arrow
					onClick={() => {
						props.deleteDaysFromList();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(35px) rotate(180deg)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>
			</div>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
				}}
			>
				<FastArrow
					onClick={() => {
						props.selectAllDays();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(43px) scale(0.8)",
						fill: "#888888",
						width: "14px",
					}}
				/>
				<FastArrow
					onClick={() => {
						props.deleteAllDays();
					}}
					style={{
						cursor: "pointer",
						fill: "#888888",
						transform: "translateY(54px) rotate(180deg) scale(0.8)",
						width: "14px",
					}}
				/>
			</div>
		</div>
	);
}

const mapState = (state) => {
	return {
		selectDays: state.pickerReducer.selectDays,
		selectedDays: state.pickerReducer.selectedDays,
	};
};

const mapDispatch = (dispatch) => {
	return {
		selectAllDays: () => dispatch(selectAllDays()),
		deleteAllDays: () => dispatch(deleteAllDays()),
		addDaysToSelect: (day) => dispatch(addDaysToSelect(day)),
		deleteDaysFromList: (day) => dispatch(deleteDaysFromList(day)),
	};
};

export default connect(mapState, mapDispatch)(DaArrow);
