import React, {useEffect} from "react";
import { connect } from "react-redux";
import HomeImg from "../../assets/icon/home.png";
import { ReactComponent as Logo } from '../../assets/icon/logo.svg';
import $ from 'jquery'

function Header(props) {
	useEffect(()=> {
		$(".icon_menu").on('click', function () {
			if ($(window).width() > 991) {
				$('body').toggleClass("nav_small");
			} else {
				$('body').toggleClass("mobile_nav");
			}
		});
	}, [])
	return (
		<header className="main-header">
			<div className="container_header">
				<div className="logo d-flex align-items-center">
					<a href="/">
						<div className="logo-default text-center">
							<Logo />
						</div>	
					</a>

					<div className="icon_menu full_menu">
						<a href="#" className="menu-toggler sidebar-toggler"></a>
					</div>
				</div>

				<div className="right_detail">
					<div className="row d-flex align-items-center pos-md-r suples">
						<div className="col-xl-12 col-9 d-flex justify-content-end">
							<div className="right_bar_top d-flex align-items-center">
								<a id='logout' className="logout" href="/logout.jsp">
									Logout
								</a>
								<a className="home-to" href="/">
									<img style={{
										minWidth: "24px",
										height: "24px"
									}} src={HomeImg} alt="" />
								</a>
							</div>
						</div>
					</div>
				</div>
			</div>
		</header>
	);
}

export default connect(null)(Header);
