import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { BASE_URL } from "../../utils/constants";
import { connect } from "react-redux";
import { setFetchBody } from "../../utils/fetchBody";
import NewNameModal from "./NewNameModal";
import { setReportName, setReportId } from "../../Redux/actions/";
import fetchReports from "../../utils/fetchReports";

function Alert(props) {
	return <MuiAlert elevation={6} variant="filled" {...props} />;
}

function SaveReportBtn(props) {
	const [modalState, setModalState] = useState(false);
	const [success, setSuccess] = useState(false);
	const [error, setError] = useState(false);
	const [name, setName] = useState("");
	const [updateState, setUpdateState] = useState(false);

	const handleSuccess = () => setSuccess(!success);
	const handleError = () => setError(!error);
	const handleSave = async (name) => {
		const fetchContent = await setFetchBody();
		delete fetchContent[undefined];
		let fetchContentWrap = {
			name: name,
			...fetchContent,
		};
		try {
			const response = await fetch(BASE_URL + "saveReport", {
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify({
					...fetchContentWrap,
				}),
			});
			const data = await response.json();
			if (data == 1) {
				handleSuccess();
				setModalState(false);
				props.setReportName(name);
				fetchReports().then((reponse) => {
					console.log("handleSave -> reponse", reponse);
					reponse.forEach((item) => {
						if (item.name == name) {
							console.log("handleSave -> name", name);
							console.log("handleSave -> item.name", item.name);
							props.setReportId(item.id);
						}
					});
				});
			} else handleError();
		} catch (error) {
			console.log("handleSave -> error", error);
		}
	};
	const handleUpdate = async () => {
		const fetchContent = await setFetchBody();
		delete fetchContent[undefined];
		let fetchContentWrap = {
			id: props.id,
			...fetchContent,
		};
		try {
			const response = await fetch(BASE_URL + "updateContent", {
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify({
					...fetchContentWrap,
				}),
			});
			setUpdateState(true);
		} catch (err) {
			console.log("handleUpdate -> err", err);
		}
	};
	const handleModalState = () => {
		props.name == "" ? setModalState(!modalState) : handleUpdate();
	};
	const handleSetName = (e) => {
		setName(e.target.value);
		console.log("handleSetName -> e.target.name", e.target.name);
	};
	useEffect(() => {
		console.log("name of modal", name);
	}, [name]);
	return (
		<div>
			<div>
				<NewNameModal
					modalState={modalState}
					handleModalState={handleModalState}
					handleSave={handleSave}
					name={name}
					handleSetName={handleSetName}
				/>
				<Snackbar
					open={success}
					autoHideDuration={6000}
					onClose={handleSuccess}
				>
					<Alert onClose={handleSuccess} severity="success">
						Success!
					</Alert>
				</Snackbar>
				<Snackbar open={error} autoHideDuration={6000} onClose={handleError}>
					<Alert onClose={handleError} severity="error">
						Name already in use
					</Alert>
				</Snackbar>
				<Snackbar
					open={updateState}
					autoHideDuration={6000}
					onClose={setUpdateState}
				>
					<Alert onClose={setUpdateState} severity="success">
						Update is success!
					</Alert>
				</Snackbar>
			</div>
			<Button className="ml-2" onClick={handleModalState} variant="success">
				Save
			</Button>
		</div>
	);
}

const mapDispath = (dispatch) => {
	return {
		setReportName: (name) => dispatch(setReportName(name)),
		setReportId: (id) => dispatch(setReportId(id)),
	};
};

const mapStateToProps = (state) => ({
	id: state.filterReducer.reportId,
	name: state.filterReducer.freportName,
});

export default connect(mapStateToProps, mapDispath)(SaveReportBtn);
