/** @format */

import React from "react";
import MonthHeader from "./MonthHeader";
import Month from "./Month";
import {
	toggleMonth,
	undoMonth,
	superOkMonth,
	clearDateHistory,
	cancelMonth,
	saveMonth,
} from "../../../../Redux/actions";
import { connect } from "react-redux";
import { Modal, Button } from "react-bootstrap";

function index(props) {
	const { shadowCols, fetchSelectMonth, fetchSelectedMonth } = props;
	const months = {
		selectMonth: [...fetchSelectMonth],
		selectedMonth: [...fetchSelectedMonth],
	};
	return (
		<>
			<Modal
				show={props.monthOpen}
				onHide={props.toggleMonth}
				backdrop="static"
				centered
				onExit={() => {
					if (props.undoMonthState.length > 0 || props.undoMonthStated.length) {
						props.clearDateHistory();
					}
				}}
			>
				<Modal.Header closeButton>
					<MonthHeader />
				</Modal.Header>

				<Modal.Body>
					<div className="data-picker">
						<Month />
					</div>
				</Modal.Body>

				<Modal.Footer>
					<Button
						variant="secondary"
						onClick={() => {
							if (
								props.undoMonthState.length > 0 ||
								props.undoMonthStated.length
							) {
								props.undoMonth();
							}
						}}
					>
						Undo
					</Button>
					<div>
						<Button
							className="mr-2"
							variant="danger"
							size="sm"
							onClick={() => {
								props.cancelMonth();
								props.toggleMonth();
							}}
						>
							Cancel
						</Button>
						<Button
							variant="success"
							onClick={() => {
								props.saveMonth();
								props.superOkMonth();
								props.toggleMonth();
							}}
							disabled={JSON.stringify(months) === JSON.stringify(shadowCols)}
						>
							Save Changes
						</Button>
					</div>
				</Modal.Footer>
			</Modal>
		</>
	);
}

const mapState = (state) => {
	return {
		monthOpen: state.monthReducer.monthOpen,
		undoMonthState: state.monthReducer.oldMonth.selectMonth,
		undoMonthStated: state.monthReducer.oldMonth.selectedMonth,
		selectMonth: state.monthReducer.selectMonth,
		selectedMonth: state.monthReducer.selectedMonth,
		shadowCols: state.monthReducer.shadowCols,
		fetchSelectMonth: state.monthReducer.fetchSelectMonth,
		fetchSelectedMonth: state.monthReducer.fetchSelectedMonth,
	};
};

const mapAction = (dispatch) => {
	return {
		superOkMonth: () => dispatch(superOkMonth()),
		toggleMonth: () => dispatch(toggleMonth()),
		undoMonth: () => dispatch(undoMonth()),
		clearDateHistory: () => dispatch(clearDateHistory()),
		cancelMonth: () => dispatch(cancelMonth()),
		saveMonth: () => dispatch(saveMonth()),
	};
};

export default connect(mapState, mapAction)(index);
