/** @format */

import React from "react";
import BranchColumn from "./BranchColumn";
import TextField from "@material-ui/core/TextField";
import { connect } from "react-redux";
import { branchSelectSearch } from "../../../Redux/actions";

function Select(props) {
	return (
		<div className="modal-body-select">
			<TextField
				InputProps={{
					className: props.classes.input,
				}}
				InputLabelProps={{
					classes: {
						root: props.classes.labelRoot,
					},
				}}
				id="outlined-basic"
				label="Search"
				variant="outlined"
				onInput={(e) => props.branchSelectSearch(e.target.value)}
			/>
			<div className="modal-filter modal-filter-select">
				{
					<BranchColumn
						openModal={props.branches.openModal}
						name="branch-select"
						itemList={props.colLeft.filter((item) => {
							let lowerText = props.text.toLowerCase(),
								lowerItem = item.toLowerCase();
							var patt = new RegExp(lowerText);
							var res = patt.test(lowerItem);
							return res;
						})}
					/>
				}
			</div>
		</div>
	);
}

const mapDisplatch = (dispatch) => ({
	branchSelectSearch: (text) => dispatch(branchSelectSearch(text)),
});

const mapState = (state) => {
	return {
		text: state.branchesReducer.selectSearchText,
	};
};

export default connect(mapState, mapDisplatch)(Select);
