import React, { useState, useEffect } from "react";
import { Modal, Form, ListGroup } from "react-bootstrap";
import { connect } from "react-redux";
import "../modal.css";

function CountersModal(props) {
	const [filterWord, setFilterWord] = useState("");
	const [filteredList, setFilteredList] = useState([]);
	const [branchesList, setBranchList] = useState([]);

	useEffect(() => {
		if (props.allBranches !== undefined && props.allBranches !== null) {
			setBranchList(Object.values(props.allBranches));
			setFilteredList(
				Object.values(props.allBranches).map((item) => (
					<ListGroup.Item
						key={item.id}
						as="a"
						href={`/heatmap/?counters=${item.id}`}
					>
						{item.name}
					</ListGroup.Item>
				)),
			);
		}
	}, [props.allBranches]);

	const hundleSetSearchWord = (event) => {
		setFilterWord(event.target.value);
		let newList = branchesList
			.filter((item) => {
				let lowerText = filterWord.toLowerCase(),
					lowerItem = item.name.toLowerCase();
				var patt = new RegExp(lowerText);
				var res = patt.test(lowerItem);
				return res;
			})
			.map((item) => (
				<ListGroup.Item
					key={item.id}
					as="a"
					href={`/heatmap/?counters=${item.id}`}
				>
					{item.name}
				</ListGroup.Item>
			));
		setFilteredList(newList);
	};

	return (
		<Modal
			show={props.show}
			backdrop="static"
			centered
			onHide={props.handlToggleCounters}
		>
			<Modal.Header closeButton>
				<span>Select branch</span>
			</Modal.Header>
			<Modal.Body>
				<div className="d-flex flex-column w-100 pl-2 pr-2">
					<Form>
						<Form.Group controlId="formBasicEmail">
							<Form.Label>Branches:</Form.Label>
							<Form.Control
								type="text"
								placeholder="Enter email"
								onChange={hundleSetSearchWord}
							/>
						</Form.Group>
					</Form>
					<ListGroup>
						{branchesList.length > 0
							? filteredList.length === 0
								? "No match"
								: filteredList
							: "List is not yet"}
					</ListGroup>
				</div>
			</Modal.Body>
		</Modal>
	);
}

const mapState = (state) => {
	return {
		allBranches: state.branchesReducer.allBranchs,
	};
};

export default connect(mapState)(CountersModal);
