import { BASE_URL } from "../../utils/constants";

const monthsArr = [
	"January",
	"February",
	"March",
	"April",
	"May",
	"June",
	"July",
	"August",
	"September",
	"October",
	"November",
	"December",
];

function getTableData(fetchBody) {
	return function(dispatch) {
		fetch(BASE_URL + "getReport1/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.text())
			.then((response) => {
				dispatch(setTableDate(response));
			});
		fetch(BASE_URL + "QmaticMap/getReport2/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.json())
			.then((response) => {
				dispatch(setCSV(response));
			});
	};
}

const setTableDate = (date) => {
	return {
		type: "ADD_TABEL_DATA",
		date,
	};
};

const setCSV = (csv) => {
	return {
		type: "ADD_CSV",
		csv,
	};
};

const concatTableDate = (date, year) => {
	return {
		type: "CONCAT_TABLE_DATA",
		date,
		year,
	};
};

const getError = (error) => {
	return {
		type: "ARRIVED_ERROR",
		error,
	};
};

const addLoader = () => {
	return {
		type: "ADD_LOADER",
	};
};

const resetTable = () => {
	return {
		type: "RESET_TABLE",
	};
};

export {
	resetTable,
	getTableData,
	setTableDate,
	concatTableDate,
	getError,
	setCSV,
	addLoader,
};
