/** @format */

import {} from "../actions/actionTypes";

const initialState = {
	showModalBranch: false,
	allBranchs: {},
	branchCols: {
		selectBranches: [],
		selectedBranches: [],
	},
	selectSearchText: "",
	selectedSearchText: "",
	oldCols: {
		selectBranches: [],
		selectedBranches: [],
	},
	temperarlyBranches: {
		selectBranches: [],
		selectedBranches: [],
	},
	shadowCols: {
		selectBranches: [],
		selectedBranches: [],
	},
	fetchCols: {
		selectBranches: [],
		selectedBranches: [],
	},
};

export default function(state = initialState, action) {
	switch (action.type) {
		case "ADD_EDITOR_ELEMENTS": {
			console.log("action", action);
			let filteredElements = Object.values(state.allBranchs)
				.filter((item) => action.branches.includes(item.id))
				.map((item) => item.name);
			let filterElements = state.branchCols.selectBranches.filter(
				(item) => !filteredElements.includes(item),
			);
			return {
				...state,
				branchCols: {
					selectBranches: [...filterElements],
					selectedBranches: [...filteredElements],
				},
				fetchCols: {
					selectBranches: [...filterElements],
					selectedBranches: [...filteredElements],
				},
			};
		}
		case "ADD_BRANCHES": {
			let BARNHES = {};
			action.branches.forEach((item) => {
				BARNHES[item.name] = {
					id: item.id,
					name: item.name,
				};
			});

			let selectBranches = action.branches.map((item) => {
				return item.name;
			});
			return {
				...state,
				allBranchs: BARNHES,
				branchCols: {
					selectBranches: [...new Set(selectBranches)],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
				oldCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
			};
		}
		case "ADD_BRANCH_TOLIS": {
			const oldSelectArr = [...state.branchCols.selectBranches],
				index = oldSelectArr.indexOf(action.branchId),
				oldArr = [...state.branchCols.selectedBranches];
			oldArr.splice(action.distIndex, 0, action.branchId);
			oldSelectArr.splice(index, 1);
			return {
				...state,
				branchCols: {
					selectBranches: oldSelectArr,
					selectedBranches: oldArr,
				},
				oldCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
			};
		}
		case "DELETE_BRANCH_FROMLIST": {
			const name = action.branchId;
			const oldSelectArrDel = [...state.branchCols.selectBranches],
				oldArr = [...state.branchCols.selectedBranches],
				index = oldArr.indexOf(name);
			oldSelectArrDel.splice(action.distIndex, 0, action.branchId);
			oldArr.splice(index, 1);
			return {
				...state,
				branchCols: {
					selectBranches: oldSelectArrDel,
					selectedBranches: oldArr,
				},
				oldCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
			};
		}
		case "ADD_BRANCHES_TOLIST": {
			const branches = state.temperarlyBranches.selectBranches;
			let oldSelectBranches = [...state.branchCols.selectBranches],
				oldSelectedbranches = [...state.branchCols.selectedBranches];
			branches.forEach((item) => {
				let index = oldSelectBranches.indexOf(item);
				oldSelectBranches.splice(index, 1);
				oldSelectedbranches.push(item);
			});
			return {
				...state,
				branchCols: {
					selectBranches: oldSelectBranches,
					selectedBranches: oldSelectedbranches,
				},
				temperarlyBranches: {
					...state.temperarlyBranches,
					selectBranches: [],
				},
				oldCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
			};
		}
		case "DELETE_BRANCHES_FROMLIST": {
			const branches = state.temperarlyBranches.selectedBranches;

			let oldSelectBranches = [...state.branchCols.selectBranches],
				oldSelectedbranches = [...state.branchCols.selectedBranches];

			branches.forEach((item) => {
				let index = oldSelectedbranches.indexOf(item);
				oldSelectedbranches.splice(index, 1);
				oldSelectBranches.push(item);
			});

			return {
				...state,
				branchCols: {
					selectBranches: oldSelectBranches,
					selectedBranches: oldSelectedbranches,
				},
				temperarlyBranches: {
					...state.temperarlyBranches,
					selectedBranches: [],
				},
				oldCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
			};
		}
		case "OPEN_BRANCH_MODAL": {
			return {
				...state,
				showModalBranch: true,
				shadowCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
			};
		}
		case "CLOSE_BRANCH_MODAL": {
			return {
				...state,
				showModalBranch: false,
			};
		}
		case "RESET_BRANCHES": {
			const selectedBranches = [...state.branchCols.selectedBranches];
			const selectBranches = [...state.branchCols.selectBranches];
			if (selectedBranches == 0) {
				return {
					...state,
					temperarlyBranches: {
						selectBranches: [],
						selectedBranches: [],
					},
				};
			}
			return {
				...state,
				branchCols: {
					selectBranches: [...selectBranches, ...selectedBranches],
					selectedBranches: [],
				},
				oldCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...state.branchCols.selectedBranches],
				},
				temperarlyBranches: {
					selectBranches: [],
					selectedBranches: [],
				},
			};
		}
		case "SELECT_ALL": {
			const selectedBranches = [...state.branchCols.selectedBranches];
			const selectBranches = [...state.branchCols.selectBranches];
			if (selectBranches.length == 0) {
				return {
					...state,
					temperarlyBranches: {
						selectBranches: [],
						selectedBranches: [],
					},
				};
			}
			return {
				...state,
				branchCols: {
					selectBranches: [],
					selectedBranches: [...selectBranches, ...selectedBranches],
				},
				oldCols: {
					selectBranches: [...state.branchCols.selectBranches],
					selectedBranches: [...selectedBranches],
				},
				temperarlyBranches: {
					selectBranches: [],
					selectedBranches: [],
				},
			};
		}
		case "UNDO_BRANCHES": {
			return {
				...state,
				branchCols: {
					selectBranches: [...state.oldCols.selectBranches],
					selectedBranches: [...state.oldCols.selectedBranches],
				},
				oldCols: {
					selectBranches: [],
					selectedBranches: [],
				},
				temperarlyBranches: {
					selectBranches: [],
					selectedBranches: [],
				},
			};
		}
		case "BRANCH_SELECT_SEARCH": {
			return {
				...state,
				selectSearchText: action.text,
			};
		}
		case "BRANCH_SELECTED_SEARCH": {
			return {
				...state,
				selectedSearchText: action.text,
			};
		}
		case "BRANCHES_OK": {
			return {
				...state,
				oldCols: {
					selectBranches: [],
					selectedBranches: [],
				},
				temperarlyBranches: {
					selectBranches: [],
					selectedBranches: [],
				},
			};
		}
		case "ADD_SELECT_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			let isHas = state.temperarlyBranches.selectBranches.includes(name);

			let newArr = [...state.temperarlyBranches.selectBranches];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}
			return {
				...state,
				temperarlyBranches: {
					...state.temperarlyBranches,
					selectBranches: newArr,
				},
			};
		}
		case "ADD_SELECTED_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			let isHas = state.temperarlyBranches.selectedBranches.includes(name);
			let newArr = [...state.temperarlyBranches.selectedBranches];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}

			return {
				...state,
				temperarlyBranches: {
					...state.temperarlyBranches,
					selectedBranches: newArr,
				},
			};
		}
		case "CLEAR_BRANCH_HISTORY": {
			return {
				...state,
				oldCols: {
					selectBranches: [],
					selectedBranches: [],
				},
				temperarlyBranches: {
					selectBranches: [],
					selectedBranches: [],
				},
				selectSearchText: "",
				selectedSearchText: "",
			};
		}
		case "CANCEL_BRANCHES": {
			return {
				...state,
				branchCols: {
					selectBranches: [...state.shadowCols.selectBranches],
					selectedBranches: [...state.shadowCols.selectedBranches],
				},
			};
		}
		case "SAVE_BRANCHES": {
			return {
				...state,
				fetchCols: {
					...state.branchCols,
				},
			};
		}
		default:
			return {
				...state,
			};
	}
}
