const PrintButton = ({ id, label, bottom, data, ...props }, ) => {
	console.log("our array", props);
	const useStyles = makeStyles((theme) => ({
		root: {
			"& > *": {
				margin: theme.spacing(1),
			},
		},
		extendedIcon: {
			marginRight: theme.spacing(1),
		},
		main: {
			position: "fixed",
			bottom: "20px",
			right: "20px",
		},
		csv: {
			width: "40px",
			height: "40px",
			position: "fixed",
			bottom: bottom * 30 + "px",
			right: "30px",
			transition: ".4s",
		},
		pdf: {
			width: "40px",
			height: "40px",
			position: "fixed",
			bottom: Math.pow(bottom, 1.4) * 30 + "px",
			right: "30px",
			transition: ".4s",
		},
	}));

	const classes = useStyles();
	return (
		<div
			style={{
				padding: "3px 10px",
				display: "inline-flex",
				alignItems: "center",
				width: "100%"
			}}
			onClick={() => {
				let name = document.getElementById("usrnm").innerText;
				let index = data[0].indexOf("Free time count");
				if(index > 0) {
					data[0][index] = "FTC"
				}
				let dateCurr = new Date();
				let doc = new jsPDF("l");
				doc.text(13, 20," Free time report");
				doc.setTextColor(100)
				doc.autoTable({
					head: [data[0]],
					body: [...data.slice(1)],
					startY: 26
				});
				let finalY = doc.lastAutoTable.finalY; // The y position on the page
				doc.setFontSize(10);
				doc.text(13, finalY + 10,"\n Created by: " + name);
				doc.text(13, finalY + 15,"\n Created date: " + dateCurr.getDate() + "." + (dateCurr.getMonth() + 1 < 10 ? "0" + (dateCurr.getMonth() + 1) : dateCurr.getMonth() + 1) + "." + dateCurr.getFullYear());
				doc.text(13, finalY + 20,"\n Selected models: " + props.filter.map(item => OBJECT_FOR_SORT[`${item}`].name).join(", "));
				doc.save('ft-report.pdf');
			}}
		>
			<FontAwesomeIcon icon={faFilePdf} /><div style={{
				marginLeft: "10px"
			}}>PDF</div>
		</div>
	);
};

const mapStore = (store) => {
	return {
		selectedBranches: store.branchesReducer.branchCols.selectedBranches,
		selectedYears: store.pickerReducer.selectedYears,
		selectedMonth: store.pickerReducer.selectedMonth,
		selectedDays: store.pickerReducer.selectedDays,
		selectedUsers: store.usersReducer.usersCols.selectedUsers,
		filter: store.filterReducer.columns.filtered_col.filItems
	};
};

export default connect(mapStore)(PrintButton);
