/** @format */

import React from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import {
	addYearToSelect,
	deleteYearToSelect,
	addYearSelectTemperarly,
	addYearSelectedTemperarly,
} from "../../../../Redux/actions";

const FilterElement = styled.div`
	padding: 0 5px;
	display: flex;
	font-size: 13px;
	line-height: 14px;
	box-shadow: "0 0 10px #333";
	cursor: pointer;
	background-color: ${(props) => {
		if (props.colname == "select") {
			return props.selectYear.includes(props.item) ? "#4b7ab1" : "";
		} else {
			return props.selectedYear.includes(props.item) ? "#4b7ab1" : "";
		}
	}};
	color: ${(props) => {
		if (props.colname == "selected") {
			return props.selectYear.includes(props.item) ? "#fff" : "333";
		} else {
			return props.selectedYear.includes(props.item) ? "#fff" : "333";
		}
	}};
	&:hover {
		background-color: #4b7ab1;
	}
`;

class YearItem extends React.Component {
	constructor(props) {
		super(props);
		this.clickCount = 0;
		this.singleClickTimer = "";
	}
	singleClick = (keyShift, keyCntrl) => {
		if (this.props.colname == "select") {
			if (keyShift) {
				this.props.addYearSelectTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addYearSelectTemperarly(this.props.item, false, true);
			} else {
				this.props.addYearSelectTemperarly(this.props.item, false, false);
			}
		} else {
			if (keyShift) {
				this.props.addYearSelectedTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addYearSelectedTemperarly(this.props.item, false, true);
			} else {
				this.props.addYearSelectedTemperarly(this.props.item, false, false);
			}
		}
	};
	handleDoubleClick = () => {
		if (this.props.colname == "select") {
			this.props.addYearToSelect(this.props.item);
		} else {
			this.props.deleteYearToSelect(this.props.item);
		}
	};
	handleClicks(keyShift, keyCntrl) {
		this.clickCount++;
		if (this.clickCount === 1) {
			this.singleClickTimer = setTimeout(
				function() {
					this.clickCount = 0;
					this.singleClick(keyShift, keyCntrl);
				}.bind(this),
				300,
			);
		} else if (this.clickCount === 2) {
			clearTimeout(this.singleClickTimer);
			this.clickCount = 0;
			this.handleDoubleClick();
		}
	}
	render() {
		return (
			<FilterElement
				{...this.props}
				onClick={(e) => {
					if (navigator.appVersion.indexOf("Win") != -1) {
						this.handleClicks(e.shiftKey, e.ctrlKey);
					} else {
						this.handleClicks(e.shiftKey, e.metaKey);
					}
				}}
			>
				<p>{this.props.item}</p>
			</FilterElement>
		);
	}
}

const mapState = (state) => {
	return {
		selectYear: state.yearReducer.temperarlyYears.selectYears,
		selectedYear: state.yearReducer.temperarlyYears.selectedYears,
	};
};

const mapDispath = (dispatch) => {
	return {
		addYearToSelect: (year) => dispatch(addYearToSelect(year)),
		deleteYearToSelect: (year) => dispatch(deleteYearToSelect(year)),
		addYearSelectTemperarly: (name, isShift, isCntrl) =>
			dispatch(addYearSelectTemperarly(name, isShift, isCntrl)),
		addYearSelectedTemperarly: (name, isShift, isCntrl) =>
			dispatch(addYearSelectedTemperarly(name, isShift, isCntrl)),
	};
};

export default connect(mapState, mapDispath)(YearItem);
