/** @format */

import React from "react";
import BranchColumn from "./BranchColumn";
import TextField from "@material-ui/core/TextField";
import { connect } from "react-redux";
import { branchSelectedSearch } from "../../../Redux/actions";

function Selected(props) {
	return (
		<div className="modal-body-selected">
			<TextField
				InputProps={{
					className: props.classes.input,
				}}
				InputLabelProps={{
					classes: {
						root: props.classes.labelRoot,
					},
				}}
				id="outlined-basic"
				label="Search"
				variant="outlined"
				onInput={(e) => props.branchSelectedSearch(e.target.value)}
			/>
			<div
				className="modal-filter modal-filter-selected"
				style={{
					marginTop: "15px",
				}}
			>
				<BranchColumn
					openModal={props.branches.openModal}
					name="branch-selected"
					itemList={props.colRighr.filter((item) => {
						let lowerText = props.text.toLowerCase(),
							lowerItem = item.toLowerCase();
						var patt = new RegExp(lowerText);
						var res = patt.test(lowerItem);
						return res;
					})}
				/>
			</div>
		</div>
	);
}

const mapDisplatch = (dispatch) => ({
	branchSelectedSearch: (text) => dispatch(branchSelectedSearch(text)),
});

const mapState = (state) => {
	return {
		text: state.branchesReducer.selectedSearchText,
	};
};

export default connect(mapState, mapDisplatch)(Selected);
