function addFilterItem(index) {
	return {
		type: ADD_FILTER_ITEM,
		payload: index,
	};
}

const addFilteredItems = (items, name, id) => {
	return {
		type: "ADD_FILTERED_ITEM_LIST",
		items,
		name,
		id,
	};
};

function superAddFiteredItems(items, name, id) {
	console.log("superAddFiteredItems -> id", id);
	return async function(dispatch) {
		await dispatch(addFilteredItems(items[0], name, id));
		await Object.keys(items[1]).forEach(async (item) => {
			console.log("superAddFiteredItems -> item", item);
			if (item == "branchIds") {
				await dispatch(addEditorElements(items[1][item]));
			} else if (item == "userIds") {
				await dispatch(addEtitorUsers(items[1][item]));
			} else if (item == "days") {
				console.log("superAddFiteredItems -> item", item, items[1][item]);
				await dispatch(addEditorDays(items[1][item]));
			} else if (item == "months") {
				await dispatch(addEditorMonths(items[1][item]));
			} else if (item == "years") {
				await dispatch(addEditorYears(items[1][item]));
			}
		});
		let body = setFetchBody();
		await dispatch(getTableData(body));
	};
}

