function sortFilteredItems(soursheIndex, distIndex) {
	return {
		type: SORT_FILTERED_ITEM,
		soursheIndex,
		distIndex,
	};
}

function resetFilters() {
	return {
		type: "RESET_FILTERS",
	};
}

function newReport() {
	return {
		type: "NEW_REPORT",
	};
}

function setReportName(name) {
	return {
		type: "SET_REPORT_NAME",
		name,
	};
}

function setReportId(id) {
	return {
		type: "SET_REPORT_ID",
		id,
	};
}

export {
	setReportId,
	setReportName,
	newReport,
	superAddFiteredItems,
	superSortFilterItem,
	superAddFiterItem,
	superAddFiteredItem,
	addFilterItem,
	addFilteredItem,
	sortFilteredItems,
	resetFilters,
	addFilteredItems,
};
