/** @format */

import React from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import {
	addMonthToSelect,
	deleteMonthToSelect,
	addMonthsToSelect,
	addMonthSelectTemperarly,
	addMonthSelectedTemperarly,
} from "../../../../Redux/actions";

const FilterElement = styled.div`
	padding: 0 5px;
	display: flex;
	font-size: 13px;
	line-height: 14px;
	box-shadow: "0 0 10px #333";
	cursor: pointer;
	background-color: ${(props) => {
		if (props.colname == "select") {
			return props.selectMonth.includes(props.item) ? "#4b7ab1" : "";
		} else {
			return props.selectedMonth.includes(props.item) ? "#4b7ab1" : "";
		}
	}};
	color: ${(props) => {
		if (props.colname == "selected") {
			return props.selectMonth.includes(props.item) ? "#fff" : "333";
		} else {
			return props.selectedMonth.includes(props.item) ? "#fff" : "333";
		}
	}};
	&:hover {
		background-color: #4b7ab1;
	}
`;

class MonthItem extends React.Component {
	constructor(props) {
		super(props);
		this.clickCount = 0;
		this.singleClickTimer = "";
	}
	singleClick = (keyShift, keyCntrl) => {
		if (this.props.colname == "select") {
			if (keyShift) {
				this.props.addMonthSelectTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addMonthSelectTemperarly(this.props.item, false, true);
			} else {
				this.props.addMonthSelectTemperarly(this.props.item, false, false);
			}
		} else {
			if (keyShift) {
				this.props.addMonthSelectedTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addMonthSelectedTemperarly(this.props.item, false, true);
			} else {
				this.props.addMonthSelectedTemperarly(this.props.item, false, false);
			}
		}
	};
	handleDoubleClick = () => {
		if (this.props.colname == "select") {
			this.props.addMonthToSelect(this.props.item);
		} else {
			this.props.deleteMonthToSelect(this.props.item);
		}
	};
	handleClicks(keyShift, keyCntrl) {
		this.clickCount++;
		if (this.clickCount === 1) {
			this.singleClickTimer = setTimeout(
				function() {
					this.clickCount = 0;
					this.singleClick(keyShift, keyCntrl);
				}.bind(this),
				300,
			);
		} else if (this.clickCount === 2) {
			clearTimeout(this.singleClickTimer);
			this.clickCount = 0;
			this.handleDoubleClick();
		}
	}
	render() {
		return (
			<FilterElement
				{...this.props}
				onClick={(e) => {
					if (navigator.appVersion.indexOf("Win") != -1) {
						this.handleClicks(e.shiftKey, e.ctrlKey);
					} else {
						this.handleClicks(e.shiftKey, e.metaKey);
					}
				}}
			>
				<p>{this.props.item}</p>
			</FilterElement>
		);
	}
}

const mapState = (state) => {
	return {
		selectMonth: state.monthReducer.temperarlyMonths.selectMonth,
		selectedMonth: state.monthReducer.temperarlyMonths.selectedMonth,
	};
};

const mapDispath = (dispatch) => {
	return {
		addMonthToSelect: (month) => dispatch(addMonthToSelect(month)),
		deleteMonthToSelect: (month) => dispatch(deleteMonthToSelect(month)),
		addMonthSelectTemperarly: (name, isShift, isCntrl) =>
			dispatch(addMonthSelectTemperarly(name, isShift, isCntrl)),
		addMonthSelectedTemperarly: (name, isShift, isCntrl) =>
			dispatch(addMonthSelectedTemperarly(name, isShift, isCntrl)),
	};
};

export default connect(mapState, mapDispath)(MonthItem);
