/** @format */

import React from "react";
import { connect } from "react-redux";
import { ReactComponent as Arrow } from "../../../assets/icon/play.svg";
import { ReactComponent as FastArrow } from "../../../assets/icon/fast-forward.svg";
import {
	addUsersToList,
	deleteUsersFromList,
	addUserToList,
	deleteUserFromList,
	resetUsers,
	selectAllUsers,
} from "../../../Redux/actions";

function UserArraw(props) {
	return (
		<div
			style={{
				display: "flex",
				flexDirection: "column",
				transform: "translateY(-5px)",
			}}
		>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
					transform: "translateY(-22px)",
				}}
			>
				<Arrow
					onClick={() => {
						props.addUsersToList();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(24px)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>

				<Arrow
					onClick={() => {
						props.deleteUsersFromList();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(35px) rotate(180deg)  scale(0.8)",
						width: "14px",
						fill: "#888888",
					}}
				/>
			</div>
			<div
				style={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					alignItems: "center",
				}}
			>
				<FastArrow
					onClick={() => {
						props.selectAllUsers();
					}}
					style={{
						cursor: "pointer",
						transform: "translateY(43px) scale(0.8)",
						fill: "#888888",
						width: "14px",
					}}
				/>
				<FastArrow
					onClick={() => {
						props.resetUsers();
					}}
					style={{
						cursor: "pointer",
						fill: "#888888",
						transform: "translateY(54px) rotate(180deg) scale(0.8)",
						width: "14px",
					}}
				/>
			</div>
		</div>
	);
}

const mapStore = (store) => {
	return {
		showModalUser: store.usersReducer.showModalUser,
		branchesCol: store.branchesReducer.branchCols,
		branches: store.branchesReducer,
		users: store.usersReducer,
		undoState: store.usersReducer.oldCols.selectUsers,
		undoStated: store.usersReducer.oldCols.selectedUsers,
	};
};

const mapDispatch = (dispatch) => {
	return {
		addUsersToList: () => dispatch(addUsersToList()),
		deleteUsersFromList: () => dispatch(deleteUsersFromList()),
		selectAllUsers: () => dispatch(selectAllUsers()),
		resetUsers: () => dispatch(resetUsers()),
		addUserToList: (userId, destination) =>
			dispatch(addUserToList(userId, destination)),
		deleteUserFromList: (userId, destination) =>
			dispatch(deleteUserFromList(userId, destination)),
	};
};

export default connect(mapStore, mapDispatch)(UserArraw);
