/** @format */

import React, { useEffect } from "react";
import { DragDropContext } from "react-beautiful-dnd";
import Column from "../Column";
import { connect } from "react-redux";
import {
	superSortFilterItem,
	superAddFiterItem,
	superAddFiteredItem,
	addFilteredItem,
	addFilterItem,
	getTableData,
	sortFilteredItems,
	setTableDate,
	resetBranches,
	resetUsers,
	resetFilters,
	clearDay,
	resetTable,
	setCSV,
	addLoader,
	clearMonth,
	clearYear,
} from "../../Redux/actions/index";
import { Button } from "react-bootstrap";

function Aside(props) {
	const addFilterElement = (e) => {
		e.preventDefault();
		e.stopPropagation();
		let itemClass = e.target.classList.contains("filter-item");
		let parentClass = e.target.parentNode.classList.contains("filter-item");
		let fromFilter = e.target.classList.contains("Filter");
		if (itemClass || parentClass) {
			let index;
			if (parentClass) {
				index = e.target.parentNode.getAttribute("data-index");
				fromFilter = e.target.parentNode.classList.contains("Filter");
			} else {
				index = e.target.getAttribute("data-index");
			}
			if (fromFilter) {
				const filteredLength =
					props.filterReducer.columns.filtered_col.filItems.length;
				const elemName = props.columns.filter_col.filItems[index];
				props.superAddFiteredItem(elemName, filteredLength);
			} else {
				const filterLength =
					props.filterReducer.columns.filter_col.filItems.length;

				const elemName = props.columns.filtered_col.filItems[index];

				switch (elemName) {
					case "branches": {
						props.resetBranches();
						break;
					}
					case "year": {
						props.clearYear();
						break;
					}
					case "month": {
						props.clearMonth();
						break;
					}
					case "day": {
						props.clearDay();
						break;
					}
					case "users": {
						props.resetUsers();
						break;
					}
				}

				props.superAddFiterItem(elemName, filterLength);
			}
		}
	};
	useEffect(() => {
		window.addEventListener("dblclick", addFilterElement);
		return () => {
			window.removeEventListener("dblclick", addFilterElement);
		};
	}, [props.filterReducer.columns.filtered_col.filItems]);
	function onDragEnd(result) {
		const { source, destination } = result;
		if (!destination) {
			return;
		}
		if (destination.droppableId !== source.droppableId) {
			if (source.droppableId == 2) {
				const elemName = props.columns.filter_col.filItems[source.index];
				props.superAddFiteredItem(elemName, destination.index);
			} else if (source.droppableId == 1) {
				const elemName = props.columns.filtered_col.filItems[source.index];
				props.superAddFiterItem(elemName);
			}
		}
		if (destination.droppableId == 1 && source.droppableId == 1) {
			props.superSortFilterItem(source.index, destination.index);
		}
	}

	return (
		<div className="filter-aside">
			<div className="filters d-flex flex-column">
				<Button
					variant="danger"
					onClick={() => {
						props.resetFilters();
						props.resetUsers();
						props.resetBranches();
						props.resetTable();
						props.clearDay();
						props.clearYear();
						props.clearMonth();
					}}
				>
					Clear all
				</Button>
				<div className="filter-wrap">
					<DragDropContext onDragEnd={onDragEnd}>
						{props.filterReducer.shownColumns.map((item, index) => {
							const filterItems = props.columns[item].filItems.map(
								(item) => props.filterItems[item],
							);
							return (
								<Column
									name={props.columns[item].title}
									key={props.columns[item].id}
									colId={props.columns[item].id}
									filterItems={filterItems}
								/>
							);
						})}
					</DragDropContext>
				</div>
			</div>
		</div>
	);
}

const mapProps = (store) => {
	return {
		filterItems: store.filterReducer.filterItems,
		columns: store.filterReducer.columns,
		shownColumns: store.filterReducer.shownColumns,
		filterReducer: store.filterReducer,
		branchesReducer: store.branchesReducer,
		isState: store.pickerReducer.isSingle,
		users: store.usersReducer,
		date: store.pickerReducer,
	};
};

const mapDispathcs = (dispatch) => {
	return {
		resetTable: () => dispatch(resetTable()),
		getTableData: (fetchBody) => dispatch(getTableData(fetchBody)),
		addLoader: () => dispatch(addLoader()),
		setCSV: (csv) => dispatch(setCSV(csv)),
		clearDay: () => dispatch(clearDay()),
		clearMonth: () => dispatch(clearMonth()),
		clearYear: () => dispatch(clearYear()),
		resetFilters: () => dispatch(resetFilters()),
		resetUsers: () => dispatch(resetUsers()),
		resetBranches: () => dispatch(resetBranches()),
		addFilteredItem: (index, distIndex) =>
			dispatch(addFilteredItem(index, distIndex)),
		superSortFilterItem: (soursheIndex, distIndex) =>
			dispatch(superSortFilterItem(soursheIndex, distIndex)),
		superAddFiterItem: (index) => dispatch(superAddFiterItem(index)),
		addFilterItem: (index) => dispatch(addFilterItem(index)),
		superAddFiteredItem: (index, distIndex) =>
			dispatch(superAddFiteredItem(index, distIndex)),
		getTableData: (branches) => dispatch(getTableData(branches)),
		sortFilteredItems: (soursheIndex, distIndex) =>
			dispatch(sortFilteredItems(soursheIndex, distIndex)),
		setTableDate: (data) => dispatch(setTableDate(data)),
	};
};

export default connect(mapProps, mapDispathcs)(Aside);
