import { BASE_URL } from "../../utils/constants";

const addLoader = () => {
	return {
		type: "ADD_LOADER",
	};
};

function openBranchModal() {
	return {
		type: "OPEN_BRANCH_MODAL",
	};
}

function closeBranchModal() {
	return {
		type: "CLOSE_BRANCH_MODAL",
	};
}

function addBranches(branches) {
	return {
		type: "ADD_BRANCHES",
		branches,
	};
}

function addBranchToList(branchId, destIndex) {
	return {
		type: "ADD_BRANCH_TOLIS",
		branchId,
		destIndex,
	};
}

function deleteBranchFromList(branchId, destIndex) {
	return {
		type: "DELETE_BRANCH_FROMLIST",
		branchId,
		destIndex,
	};
}

function resetBranches() {
	return {
		type: "RESET_BRANCHES",
	};
}

function selectAll() {
	return {
		type: "SELECT_ALL",
	};
}

function branchSelectSearch(text) {
	return {
		type: "BRANCH_SELECT_SEARCH",
		text,
	};
}

function branchSelectedSearch(text) {
	return {
		type: "BRANCH_SELECTED_SEARCH",
		text,
	};
}

function undoBranches() {
	return {
		type: "UNDO_BRANCHES",
	};
}

function okBranches() {
	return {
		type: "BRANCHES_OK",
	};
}

const countMask = ["first", "second", "third", "fourth", "fifth"];

const setTableDate = (date) => {
	return {
		type: "ADD_TABEL_DATA",
		date,
	};
};

const setCSV = (csv) => {
	return {
		type: "ADD_CSV",
		csv,
	};
};

function getTableData(fetchBody) {
	return function(dispatch) {
		fetch(BASE_URL + "getReport1/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.text())
			.then((response) => {
				dispatch(setTableDate(response));
			});
		fetch(BASE_URL + "getReport2/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.json())
			.then((response) => {
				dispatch(setCSV(response));
			});
	};
}

function clearBranchHistory() {
	return {
		type: "CLEAR_BRANCH_HISTORY",
	};
}

function saveBranches() {
	return {
		type: "SAVE_BRANCHES",
	};
}

function superOk() {
	return async function(dispatch, getState) {
		await dispatch(addLoader());
		let fetchBody = {};
		function setFetchBody() {
			const filteredCol = getState().filterReducer.columns.filtered_col;

			filteredCol.filItems.forEach((item, index) => {
				if (
					item != "total_free_time" ||
					item != "count" ||
					item != "max_free_time" ||
					item != "total_session_time"
				) {
					let name = item;
					if (item == "branches") {
						name = "branchName";
					} else if (item == "users") {
						name = "fullname";
					}
					fetchBody[countMask[index]] = {
						[`${name}`]: true,
					};
				}
			});
			filteredCol.filItems.forEach((item) => {
				if (item == "total_free_time") {
					fetchBody["totalFreeTime"] = true;
				} else if (item == "max_free_time") {
					fetchBody["maxFreeTime"] = true;
				} else if (item == "total_session_time") {
					fetchBody["totalSessionTime"] = true;
				} else if (item == "count") {
					fetchBody["freeCount"] = true;
				}
			});
			const {
				allBranchs,
				branchCols: { selectedBranches },
			} = getState().branchesReducer;
			fetchBody.branchIds = [];
			selectedBranches.forEach((item) => {
				fetchBody.branchIds.push(allBranchs[item].id);
			});
			const {
				allUsers,
				usersCols: { selectedUsers },
			} = getState().usersReducer;
			fetchBody.userIds = [];
			selectedUsers.forEach((item) => {
				fetchBody.userIds.push(allUsers[item].id);
			});

			const { fetchSelectDays } = getState().pickerReducer;
			const { fetchSelectYears } = getState().yearReducer;
			const { fetchSelectMonth } = getState().monthReducer;
			fetchBody.years = fetchSelectYears.map((item) => item);
			fetchBody.months = fetchSelectMonth.map((item) => item);
			fetchBody.days = fetchSelectDays.map((item) => item);
		}
		await dispatch(okBranches());
		await setFetchBody();
		await dispatch(getTableData(fetchBody));
	};
}

