/** @format */

import React from "react";
import YearHeader from "./YearHeader";
import Year from "./Year";
import {
	toggleYear,
	undoYear,
	superOkYear,
	clearDateHistory,
	cancelYears,
	saveYear,
} from "../../../../Redux/actions";
import { connect } from "react-redux";
import { Modal, Button } from "react-bootstrap";

function index(props) {
	const { shadowCols, fetchSelectYears, fetchSelectedYears } = props;
	const years = {
		selectYears: [...fetchSelectYears],
		selectedYears: [...fetchSelectedYears],
	};
	return (
		<>
			<Modal
				show={props.yearOpen}
				onHide={props.toggleYear}
				backdrop="static"
				centered
				onExit={() => {
					if (
						props.undoYearState.length > 0 ||
						props.undoYearStated.length > 0
					) {
						props.clearDateHistory();
					}
				}}
			>
				<Modal.Header closeButton>
					<YearHeader />
				</Modal.Header>
				<Modal.Body>
					<Year />
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="secondary"
						onClick={() => {
							if (
								props.undoYearState.length > 0 ||
								props.undoYearStated.length > 0
							) {
								props.undoYear();
							}
						}}
					>
						Undo
					</Button>
					<div>
						<Button
							className="mr-2"
							variant="danger"
							onClick={() => {
								props.cancelYears();
								props.toggleYear();
							}}
						>
							Cancel
						</Button>
						<Button
							variant="success"
							onClick={() => {
								props.saveYear();
								props.superOkYear();
								props.toggleYear();
							}}
							disabled={JSON.stringify(years) === JSON.stringify(shadowCols)}
						>
							Save Changes
						</Button>
					</div>
				</Modal.Footer>
			</Modal>
		</>
	);
}

const mapState = (state) => {
	return {
		yearOpen: state.yearReducer.yearOpen,
		selectYears: state.yearReducer.selectYears,
		selectedYears: state.yearReducer.selectedYears,
		shadowCols: state.yearReducer.shadowCols,
		undoYearState: state.yearReducer.oldYear.selectYears,
		undoYearStated: state.yearReducer.oldYear.selectedYears,
		fetchSelectYears: state.yearReducer.fetchSelectYears,
		fetchSelectedYears: state.yearReducer.fetchSelectedYears,
	};
};

const mapAction = (dispatch) => {
	return {
		superOkYear: () => dispatch(superOkYear()),
		toggleYear: () => dispatch(toggleYear()),
		undoYear: () => dispatch(undoYear()),
		clearDateHistory: () => dispatch(clearDateHistory()),
		cancelYears: () => dispatch(cancelYears()),
		saveYear: () => dispatch(saveYear()),
	};
};

export default connect(mapState, mapAction)(index);
