/** @format */

import React from "react";
import { connect } from "react-redux";
import {
	addUsersToList,
	deleteUsersFromList,
	addUserToList,
	deleteUserFromList,
	closeUserModal,
	resetUsers,
	selectAllUsers,
	undoUsers,
	superOkUser,
	clearUserHistory,
	cancelUsers,
	saveUsers,
} from "../../../Redux/actions";
import { withStyles } from "@material-ui/core/styles";
import Selected from "./Selected";
import Select from "./Select";
import { Modal, Button } from "react-bootstrap";
import UserArraw from "./UserArraw";

const styles = (theme) => ({
	input: {
		height: 32,
	},
	labelRoot: {
		fontSize: 14,
		transform: "translate(14px, 10px) scale(1)",
	},
});

const DragModal = withStyles(styles)((props) => {
	const { classes, shadowCols, fetchCols } = props;
	var colLeft = props.users.usersCols.selectUsers;
	var colRighr = props.users.usersCols.selectedUsers;

	return (
		<>
			<Modal
				show={props.showModalUser}
				onHide={props.closeUserModal}
				backdrop="static"
				onExit={() => {
					if (props.undoState.length > 0 || props.undoStated.length > 0) {
						props.clearUserHistory();
					}
				}}
				centered
			>
				<Modal.Header closeButton>
					<span>Users filter</span>
				</Modal.Header>
				<Modal.Body>
					<Select classes={classes} users={props.users} colLeft={colLeft} />
					<UserArraw />
					<Selected classes={classes} users={props.users} colRighr={colRighr} />
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="secondary"
						onClick={() => {
							if (props.undoState.length > 0 || props.undoStated.length > 0) {
								props.undoUsers();
							}
						}}
					>
						Undo
					</Button>
					<div>
						<Button
							className="mr-2"
							variant="danger"
							onClick={() => {
								props.saveUsers();
								props.cancelUsers();
								props.closeUserModal();
							}}
						>
							Cancel
						</Button>
						<Button
							variant="primary"
							onClick={() => {
								props.superOkUser();
								props.closeUserModal();
							}}
							disabled={
								JSON.stringify(fetchCols) === JSON.stringify(shadowCols)
							}
						>
							Save Changes
						</Button>
					</div>
				</Modal.Footer>
			</Modal>
		</>
	);
});

const mapStore = (store) => {
	return {
		showModalUser: store.usersReducer.showModalUser,
		usersCols: store.usersReducer.usersCols,
		shadowCols: store.usersReducer.shadowCols,
		users: store.usersReducer,
		undoState: store.usersReducer.oldCols.selectUsers,
		undoStated: store.usersReducer.oldCols.selectedUsers,
		fetchCols: store.usersReducer.fetchCols,
	};
};

const mapDispatch = (dispatch) => {
	return {
		addUsersToList: () => dispatch(addUsersToList()),
		deleteUsersFromList: () => dispatch(deleteUsersFromList()),
		superOkUser: () => dispatch(superOkUser()),
		undoUsers: () => dispatch(undoUsers()),
		selectAllUsers: () => dispatch(selectAllUsers()),
		resetUsers: () => dispatch(resetUsers()),
		addUserToList: (userId, destination) =>
			dispatch(addUserToList(userId, destination)),
		deleteUserFromList: (userId, destination) =>
			dispatch(deleteUserFromList(userId, destination)),
		closeUserModal: () => dispatch(closeUserModal()),
		clearUserHistory: () => dispatch(clearUserHistory()),
		cancelUsers: () => dispatch(cancelUsers()),
		saveUsers: () => dispatch(saveUsers()),
	};
};

export default connect(mapStore, mapDispatch)(DragModal);
