function saveMonth() {
	return {
		type: "SAVE_MONTH",
	};
}

function superOkMonth() {
	return async function(dispatch, getState) {
		await dispatch(addLoader());
		let fetchBody = {};
		function setFetchBody() {
			const filteredCol = getState().filterReducer.columns.filtered_col;
			filteredCol.filItems.forEach((item, index) => {
				if (
					item != "total_free_time" ||
					item != "count" ||
					item != "max_free_time" ||
					item != "total_session_time"
				) {
					let name = item;
					if (item == "branches") {
						name = "branchName";
					} else if (item == "users") {
						name = "fullname";
					}
					fetchBody[countMask[index]] = {
						[`${name}`]: true,
					};
				}
			});
			filteredCol.filItems.forEach((item) => {
				if (item == "total_free_time") {
					fetchBody["totalFreeTime"] = true;
				} else if (item == "max_free_time") {
					fetchBody["maxFreeTime"] = true;
				} else if (item == "total_session_time") {
					fetchBody["totalSessionTime"] = true;
				} else if (item == "count") {
					fetchBody["freeCount"] = true;
				}
			});
			const {
				allBranchs,
				branchCols: { selectedBranches },
			} = getState().branchesReducer;
			fetchBody.branchIds = [];
			selectedBranches.forEach((item) => {
				fetchBody.branchIds.push(allBranchs[item].id);
			});
			const {
				allUsers,
				usersCols: { selectedUsers },
			} = getState().usersReducer;
			fetchBody.userIds = [];
			selectedUsers.forEach((item) => {
				fetchBody.userIds.push(allUsers[item].id);
			});

			const { fetchSelectDays } = getState().pickerReducer;
			const { fetchSelectYears } = getState().yearReducer;
			const { fetchSelectMonth } = getState().monthReducer;
			fetchBody.years = fetchSelectYears.map((item) => item);
			fetchBody.months = fetchSelectMonth.map((item) => item);
			fetchBody.days = fetchSelectDays.map((item) => item);
		}
		await dispatch(okMOnth());
		await setFetchBody();
		await dispatch(getTableData(fetchBody));
	};
}

function cancelMonth() {
	return {
		type: "CANCEL_MONTH",
	};
}

function clearMonth() {
	return {
		type: "CLEAR_MONTH",
	};
}

function addEditorMonths(months) {
	return {
		type: "ADD_EDITOR_MONTHS",
		months,
	};
}

export {
	addEditorMonths,
	saveMonth,
	cancelMonth,
	clearMonth,
	clearDateHistory,
	deleteMonthsFromList,
	addMonthsToSelect,
	addMonthSelectTemperarly,
	addMonthSelectedTemperarly,
	superOkMonth,
	okMOnth,
	undoMonth,
	toggleMonth,
	resetMonth,
	addMonthToSelect,
	deleteMonthToSelect,
	selectAllMonth,
	deleteAllMonth,
};
