/** @format */

import React from "react";
import { connect } from "react-redux";
import {
	openBranchModal,
	openUserModal,
	addSelectUserTemperarly,
	addSelectedUserTemperarly,
	addUserToList,
	deleteUserFromList,
} from "../../../Redux/actions";
import styled from "styled-components";

const FilterElementuser = styled.div`
	padding: 0 5px;
	display: flex;
	font-size: 13px;
	line-height: 14px;
	box-shadow: "0 0 10px #333";
	cursor: pointer;
	background-color: ${(props) => {
		if (props.colname == "user-select") {
			return props.selectUsers.includes(props.name) ? "#4b7ab1" : "";
		} else {
			return props.selectedUsers.includes(props.name) ? "#4b7ab1" : "";
		}
	}};
	color: ${(props) => {
		if (props.colname == "user-select") {
			return props.selectUsers.includes(props.name) ? "#fff" : "333";
		} else {
			return props.selectedUsers.includes(props.name) ? "#fff" : "333";
		}
	}};
	&:hover {
		background-color: #4b7ab1;
	}
`;

class ModaluserFilter extends React.Component {
	constructor(props) {
		super(props);
		this.clickCount = 0;
		this.singleClickTimer = "";
	}
	singleClick = (keyShift, keyCntrl) => {
		console.log("ModaluserFilter -> singleClick -> this.props", this.props);

		if (this.props.colname == "user-select") {
			if (keyShift) {
				this.props.addSelectUserTemperarly(this.props.name, true, false);
			} else if (keyCntrl) {
				this.props.addSelectUserTemperarly(this.props.name, false, true);
			} else {
				this.props.addSelectUserTemperarly(this.props.name, false, false);
			}
		} else {
			if (keyShift) {
				this.props.addSelectedUserTemperarly(this.props.name, true, false);
			} else if (keyCntrl) {
				this.props.addSelectedUserTemperarly(this.props.name, false, true);
			} else {
				this.props.addSelectedUserTemperarly(this.props.name, false, false);
			}
		}
	};
	handleDoubleClick = () => {
		if (this.props.colname == "user-select") {
			this.props.addUserToList(this.props.name);
		} else {
			this.props.deleteUserFromList(this.props.name);
		}
	};
	handleClicks(keyShift, keyCntrl) {
		this.clickCount++;
		if (this.clickCount === 1) {
			this.singleClickTimer = setTimeout(
				function() {
					this.clickCount = 0;
					this.singleClick(keyShift, keyCntrl);
				}.bind(this),
				300,
			);
		} else if (this.clickCount === 2) {
			clearTimeout(this.singleClickTimer);
			this.clickCount = 0;
			this.handleDoubleClick();
		}
	}
	render() {
		const { name } = this.props;
		return (
			<FilterElementuser
				{...this.props}
				onClick={(e) => {
					if (navigator.appVersion.indexOf("Win") != -1) {
						this.handleClicks(e.shiftKey, e.ctrlKey);
					} else {
						this.handleClicks(e.shiftKey, e.metaKey);
					}
				}}
			>
				<p>{this.props.users[name].name}</p>
			</FilterElementuser>
		);
	}
}

const mapStore = (store) => {
	return {
		users: store.usersReducer.allUsers,
		selectUsers: store.usersReducer.temperarlyUsers.selectUser,
		selectedUsers: store.usersReducer.temperarlyUsers.selectedUser,
	};
};

const mapDispath = (dispatch) => {
	return {
		addUserToList: (branchId) => dispatch(addUserToList(branchId)),
		deleteUserFromList: (branchId) => dispatch(deleteUserFromList(branchId)),
		addSelectUserTemperarly: (name, isShift, isCntrl) =>
			dispatch(addSelectUserTemperarly(name, isShift, isCntrl)),
		addSelectedUserTemperarly: (name, isShift, isCntrl) =>
			dispatch(addSelectedUserTemperarly(name, isShift, isCntrl)),
		openBranchModal: () => dispatch(openBranchModal()),
		openUserModal: () => dispatch(openUserModal()),
	};
};

export default connect(mapStore, mapDispath)(ModaluserFilter);
