/** @format */

import React from "react";
import ClearImg from "../../../../assets/icon/content/clear_24px_outlined.svg";
import { connect } from "react-redux";
import { toggleMonth } from "../../../../Redux/actions";

function MonthHeader(props) {
	return <span>Month</span>;
}

const mapAction = (dispatch) => {
	return {
		toggleMonth: () => dispatch(toggleMonth()),
	};
};

export default connect(null, mapAction)(MonthHeader);
