/** @format */

import React, { Component } from "react";
import Aside from "./Aside/Aside";
import { connect } from "react-redux";
import UserModal from "./Modals/Users";
import BranchModal from "./Modals/Branches";
import YearModal from "./Modals/Date/Year/";
import MonthModal from "./Modals/Date/Month/";
import DayModal from "./Modals/Date/Day";
import TableWrap from "./TableWrap";
import { addBranches, addUsers } from "../Redux/actions/index";
import SideBar from "./SideBar/SideBar";
import ReportList from "./ReportList";

import {
	faFileExport,
	faFilePdf,
	faFileCsv,
} from "@fortawesome/free-solid-svg-icons";

import { CSVLink, CSVDownload } from "react-csv";

import PrintButton from "./PrintBtn/PrintBtn";
import ReactExport from "react-export-excel";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { DropdownButton, Dropdown, Button } from "react-bootstrap";
import Preloader from "./Preloader/Preloader";
import { BASE_URL } from "../utils/constants";
import SaveReportBtn from "./ReportList/SaveReportBtn";

class Content extends Component {
	constructor(props) {
		super(props);
		this.ref = React.createRef();
	}

	componentDidMount() {
		fetch(BASE_URL + "getReportBranchList/")
			.then((response) => response.json())
			.then((response) => this.props.addBranches(response));

		fetch(BASE_URL + "getReportUserList/")
			.then((response) => response.json())
			.then((response) => this.props.addUsers(response));
	}

	render() {
		console.log("this props -------------", this.props.reportSelected);
		return (
			<div className="container_full">
				<UserModal />
				<BranchModal />
				<YearModal />
				<MonthModal />
				<DayModal />
				<SideBar />
				{!this.props.reportSelected ? (
					<ReportList />
				) : (
					<div
						className="content_wrapper d-flex flex-column"
						style={{
							padding: "0",
						}}
					>
						<div className="content-head">
							<div
								style={{
									width: "100%",
									padding: "10px",
									background: "#fff",
								}}
								className="d-flex justify-content-between align-items-center pt-3 pb-3 pl-4 pr-4"
							>
								<h1>
									{this.props.freportName == ""
										? "New report"
										: this.props.freportName}
								</h1>
								<div className="d-flex">
									{this.props.table.tableIsLoading && (
										<Preloader className="mr-4" />
									)}
									<DropdownButton
										id="dropdown-basic-button"
										title={
											<span>
												<FontAwesomeIcon icon={faFileExport} /> Export
											</span>
										}
										disabled={this.props.table.tableCSV.length === 0}
									>
										<Dropdown.Item href="#/action-2" className="custom-dd">
											<CSVLink
												filename={"ft-report.csv"}
												data={this.props.table.tableCSV}
												style={{
													padding: "3px 10px",
													display: "inline-block",
													width: "100%",
												}}
											>
												<FontAwesomeIcon icon={faFileCsv} />
												CSV
											</CSVLink>
										</Dropdown.Item>
										<Dropdown.Item href="#/action-3" className="custom-dd">
											<PrintButton
												id={"#ourPdf > table"}
												data={this.props.table.tableCSV}
											/>
										</Dropdown.Item>
									</DropdownButton>

									<SaveReportBtn />
								</div>
							</div>
						</div>
						<div className="m-3 d-flex">
							<Aside />
							<TableWrap error={this.props.store.arrivedError} />
						</div>
					</div>
				)}
			</div>
		);
	}
}

const mapDispatch = (dispatch) => {
	return {
		addUsers: (users) => dispatch(addUsers(users)),
		addBranches: (branches) => dispatch(addBranches(branches)),
	};
};

const mapStore = (state) => {
	console.log("mapStore -> state", state);
	return {
		table: state.tableReducer,
		store: state.branchesReducer,
		reportSelected: state.filterReducer.reportLesected,
		freportName: state.filterReducer.freportName,
	};
};

export default connect(mapStore, mapDispatch)(Content);
