/** @format */

import { SORT_FILTERED_ITEM } from "../actions/actionTypes";

const OBJECT_FOR_SORT = {
	branches: {
		widthparam: true,
	},
	users: {
		widthparam: true,
	},
	year: {
		widthparam: true,
	},
	month: {
		widthparam: true,
	},
	day: {
		widthparam: true,
	},
	count: {
		widthparam: false,
	},
	max_free_time: {
		widthparam: false,
	},
	total_free_time: {
		widthparam: false,
	},
	total_session_time: {
		widthparam: false,
	},
};

const initialState = {
	reportLesected: false,
	freportName: "",
	reportId: "",
	filterItems: {
		branches: {
			id: "1",
			name: "Branches",
			widthparam: true,
			shouldFollow: false,
			followPriority: 0,
		},
		users: {
			id: "5",
			name: "Users",
			widthparam: true,
			shouldFollow: false,
			followPriority: 0,
		},
		count: {
			id: "8",
			name: "Free time count",
			widthparam: false,
			shouldFollow: false,
			followPriority: 0,
		},
		max_free_time: {
			id: "7",
			name: "Max free time",
			widthparam: false,
			shouldFollow: false,
			followPriority: 0,
		},
		total_free_time: {
			id: "6",
			name: "Total free time",
			widthparam: false,
			shouldFollow: false,
			followPriority: 0,
		},
		year: {
			id: "2",
			name: "Year",
			widthparam: true,
			shouldFollow: true,
			followPriority: 1,
		},
		month: {
			id: "3",
			name: "Month",
			widthparam: true,
			shouldFollow: true,
			followPriority: 2,
		},
		day: {
			id: "4",
			name: "Day",
			widthparam: true,
			shouldFollow: true,
			followPriority: 3,
		},
		total_session_time: {
			id: "9",
			name: "Total session time",
			widthparam: false,
			shouldFollow: false,
			followPriority: 0,
		},
	},
	selectedFilterItems: [],
	columns: {
		filtered_col: {
			id: "1",
			title: "Filtered",
			filItems: [],
		},
		filter_col: {
			id: "2",
			title: "Filter",
			filItems: [
				"branches",
				"year",
				"month",
				"day",
				"users",
				"total_free_time",
				"max_free_time",
				"count",
				"total_session_time",
			],
		},
	},
	shownColumns: ["filtered_col", "filter_col"],
};

export default function(state = initialState, action) {
	switch (action.type) {
		case "ADD_FILTERED_ITEM": {
			const index = state.columns.filter_col.filItems.indexOf(action.index),
				cutArr = state.columns.filter_col.filItems.slice(index, index + 1),
				oldArr = [...state.columns.filtered_col.filItems];
			oldArr.splice(action.distIndex, 0, action.index);
			const arrWidthParam = oldArr.filter(
				(item) => OBJECT_FOR_SORT[item].widthparam,
			);
			const arrWidthOutParam = oldArr.filter(
				(item) => !OBJECT_FOR_SORT[item].widthparam,
			);
			const arrWidthDouble = [...arrWidthParam, ...arrWidthOutParam];
			return {
				...state,
				columns: {
					...state.columns,
					filtered_col: {
						...state.columns.filtered_col,
						filItems: [...arrWidthDouble],
					},
					filter_col: {
						...state.columns.filter_col,
						filItems: state.columns.filter_col.filItems.filter(
							(item) => item != cutArr[0],
						),
					},
				},
			};
		}
		case "ADD_FILTER_ITEM": {
			const index = state.columns.filtered_col.filItems.indexOf(action.payload),
				cutArr = state.columns.filtered_col.filItems.slice(index, index + 1),
				newArr = [...state.columns.filter_col.filItems, ...cutArr];
			const sortedArr = newArr.sort(function(a, b) {
				const aVal = state.filterItems[a].id,
					bVal = state.filterItems[b].id;
				return parseInt(aVal) - parseInt(bVal);
			});
			return {
				...state,
				columns: {
					...state.columns,
					filter_col: {
						...state.columns.filter_col,
						filItems: [...sortedArr],
					},
					filtered_col: {
						...state.columns.filtered_col,
						filItems: state.columns.filtered_col.filItems.filter(
							(item) => item != cutArr[0],
						),
					},
				},
			};
		}
		case "ADD_FILTERED_ITEM_LIST": {
			return {
				...state,
				reportLesected: true,
				freportName: action.name,
				reportId: action.id,
				columns: {
					...state.columns,
					filtered_col: {
						...state.columns.filtered_col,
						filItems: [...action.items],
					},
					filter_col: {
						...state.columns.filter_col,
						filItems: state.columns.filter_col.filItems.filter(
							(item) => !action.items.includes(item),
						),
					},
				},
			};
		}
		case SORT_FILTERED_ITEM: {
			let oldArr = [...state.columns.filtered_col.filItems];
			let cutedArr = oldArr.splice(action.soursheIndex, 1);
			oldArr.splice(action.distIndex, 0, cutedArr);
			const arrWidthParam = oldArr.filter(
				(item) => OBJECT_FOR_SORT[item].widthparam,
			);
			const arrWidthOutParam = oldArr.filter(
				(item) => !OBJECT_FOR_SORT[item].widthparam,
			);
			const arrWidthDouble = [...arrWidthParam, ...arrWidthOutParam];

			return {
				...state,
				columns: {
					...state.columns,
					filtered_col: {
						...state.columns.filtered_col,
						filItems: [...arrWidthDouble],
					},
				},
			};
		}
		case "RESET_FILTERS": {
			return {
				...state,
				columns: {
					...state.columns,
					filtered_col: {
						...state.columns.filtered_col,
						filItems: [],
					},
					filter_col: {
						...state.columns.filter_col,
						filItems: [
							"branches",
							"year",
							"month",
							"day",
							"users",
							"total_free_time",
							"max_free_time",
							"count",
							"total_session_time",
						],
					},
				},
			};
		}
		case "NEW_REPORT": {
			return {
				...state,
				reportLesected: true,
			};
		}
		case "SET_REPORT_NAME": {
			return {
				...state,
				freportName: action.name,
			};
		}
		case "SET_REPORT_ID": {
			return {
				...state,
				reportId: action.id,
			};
		}
		default:
			return state;
	}
}
