import React from "react";
import { Modal, Form, Button } from "react-bootstrap";
import styled from "styled-components";

const MapStyle = styled(Modal.Header)`
	padding: 0 10px !important;
`;

export default function NewNameModal({
	handleSave,
	modalState,
	handleModalState,
	name,
	handleSetName,
}) {
	return (
		<Modal show={modalState} onHide={handleModalState} centered>
			<MapStyle closeButton>
				<Modal.Title>Create report</Modal.Title>
			</MapStyle>
			<Modal.Body>
				<Form.Group
					style={{
						width: "93%",
					}}
				>
					<Form.Label>Report name</Form.Label>
					<Form.Control
						type="text"
						placeholder="Enter report name"
						onChange={handleSetName}
					/>
				</Form.Group>
			</Modal.Body>
			<Modal.Footer>
				<Button variant="light">Cancel</Button>
				<Button onClick={() => handleSave(name)} variant="success">
					Save
				</Button>
			</Modal.Footer>
		</Modal>
	);
}
