/** @format */

import React from "react";
import UserColumn from "./UserColumn";
import TextField from "@material-ui/core/TextField";
import { connect } from "react-redux";
import {
	userSelectedSearch,
	addUsersToList,
	deleteUsersFromList,
} from "../../../Redux/actions";

function Selected(props) {
	return (
		<div className="modal-body-selected">
			<TextField
				InputProps={{
					className: props.classes.input,
				}}
				InputLabelProps={{
					classes: {
						root: props.classes.labelRoot,
					},
				}}
				id="outlined-basic"
				label="Search"
				variant="outlined"
				onInput={(e) => props.userSelectedSearch(e.target.value)}
			/>
			<div
				className="modal-filter modal-filter-selected"
				style={{
					minHeight: "100%",
				}}
			>
				<UserColumn
					name="user-selected"
					itemList={props.colRighr.filter((item) => {
						let lowerText = props.text.toLowerCase(),
							lowerItem = item.toLowerCase();
						var patt = new RegExp(lowerText);
						var res = patt.test(lowerItem);
						return res;
					})}
				/>
			</div>
		</div>
	);
}

const mapState = (state) => {
	return {
		text: state.usersReducer.selectedSearchText,
	};
};

const mapDispatch = (dispatch) => {
	return {
		userSelectedSearch: (text) => dispatch(userSelectedSearch(text)),
	};
};

export default connect(mapState, mapDispatch)(Selected);
