/** @format */

import React from "react";
import { connect } from "react-redux";
import {
	openBranchModal,
	openUserModal,
	toggleYear,
	toggleDay,
	toggleMonth,
} from "../Redux/actions/index";

function FilterItem(props) {
	const itemClass = props.widthConf
		? "filter-item filter-item-f" + " " + props.colName
		: "filter-item" + " " + props.colName;
	const nameD = props.name;
	return (
		<div
			style={{
				padding: "10px",
				backgroundColor: "red",
				display: "flex",
				boxShadow: props.isDrag ? "0 0 10px #333" : "",
			}}
			className={itemClass}
			ref={props.INNREF}
			{...props.INNDRAGGUBLE}
			{...props.HUNDLER}
			data-index={props.elemIndex}
		>
			<span
				style={{
					transform: "translateX(10px)",
				}}
			>
				{nameD}
			</span>
			{props.widthConf && (
				<svg
					onClick={() => {
						if (nameD == "Branches") {
							props.openBranchModal();
						} else if (nameD == "Users") {
							props.openUserModal();
						} else if (nameD == "Year") {
							props.toggleYear();
						} else if (nameD == "Month") {
							props.toggleMonth();
						} else if (nameD == "Day") {
							props.toggleDay();
						}
					}}
					data-filkind="branches"
					className="filter-icon filter-branches branches-icon"
					fill="#ffffff"
					height="21"
					viewBox="0 0 24 24"
					width="21"
					xmlns="http://www.w3.org/2000/svg"
				>
					<path d="M10 18h4v-2h-4v2zM3 6v2h18V6H3zm3 7h12v-2H6v2z"></path>
					<path d="M0 0h24v24H0z" fill="none"></path>
				</svg>
			)}
		</div>
	);
}

const mapDispath = (dispatch) => {
	return {
		openBranchModal: () => dispatch(openBranchModal()),
		openUserModal: () => dispatch(openUserModal()),
		toggleYear: () => dispatch(toggleYear()),
		toggleDay: () => dispatch(toggleDay()),
		toggleMonth: () => dispatch(toggleMonth()),
	};
};

export default connect(null, mapDispath)(FilterItem);
