/** @format */

import React from "react";
import { connect } from "react-redux";
import { setTableDate } from "../Redux/actions";
import ReactHtmlParser, {
	processNodes,
	convertNodeToElement,
	htmlparser2,
} from "react-html-parser";
import { faFile } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function TableWrap(props) {
	const ref = React.createRef();
	return (
		<div
			style={{
				flexGrow: 1,
				color: "white",
				display: "flex",
				position: "relative",
				overflowX: "auto",
				height: "calc(100vh - 84px)",
			}}
		>
			<div
				style={{
					minWidth: "100%",
				}}
			>
				{props.table.tableData.length < 121 ? (
					<div
						style={{
							position: "absolute",
							left: 0,
							right: 0,
							margin: "auto",
							paddingTop: "40px",
							height: "100%",
							display: "flex",
							alignItems: "flex-start",
							justifyContent: "center",
							fontSize: "40px",
						}}
					>
						{props.table.tableIsLoading && props.tableData === "" ? (
							<div className="lds-dual-ring"></div>
						) : (
							<div className="d-flex flex-column align-items-center">
								<span
									style={{
										color: "#333",
										fontSize: "24px",
										marginBottom: "30px",
									}}
								>
									Please select models to create report
								</span>
								<div
									style={{
										transform: "scale(2)",
										color: "#607d8b",
									}}
								>
									<FontAwesomeIcon icon={faFile} />
								</div>
							</div>
						)}
					</div>
				) : (
					<div>
						{
							<div>
								<div ref={ref} id={"ourPdf"}>
									{ReactHtmlParser(props.table.tableData.toLowerCase())}
								</div>
							</div>
						}
					</div>
				)}
			</div>
		</div>
	);
}

const mapStore = (store) => {
	return {
		table: store.tableReducer,
	};
};

const mapAction = (dispatch) => {
	return {
		setTableDate: (data) => dispatch(setTableDate(data)),
	};
};

export default connect(mapStore, mapAction)(TableWrap);
