/** @format */

import React from "react";
import DayHeader from "./DayHeader";
import Day from "./Day";
import { connect } from "react-redux";
import {
	toggleDay,
	undoDays,
	superOkDays,
	clearDateHistory,
	cancelDays,
	saveDays,
} from "../../../../Redux/actions";
import { Modal, Button } from "react-bootstrap";

function index(props) {
	const { shadowCols, fetchSelectDays, fetchSelectedDays } = props;
	const days = {
		selectDays: [...fetchSelectDays],
		selectedDays: [...fetchSelectedDays],
	};
	return (
		<>
			<Modal
				show={props.dayOpen}
				onHide={props.toggleDay}
				backdrop="static"
				onExit={() => {
					if (props.undoDayState.length > 0 || props.undoDayStated.length > 0) {
						props.clearDateHistory();
					}
				}}
				centered
			>
				<Modal.Header closeButton>
					<DayHeader />
				</Modal.Header>
				<Modal.Body>
					<div className="data-picker">
						<Day />
					</div>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="secondary"
						onClick={() => {
							if (
								props.undoDayState.length > 0 ||
								props.undoDayStated.length > 0
							) {
								props.undoDays();
							}
						}}
					>
						Undo
					</Button>
					<div>
						<Button
							className="mr-2"
							variant="danger"
							size="sm"
							onClick={() => {
								props.cancelDays();
								props.toggleDay();
							}}
						>
							Cancel
						</Button>
						<Button
							variant="success"
							onClick={() => {
								props.saveDays();
								props.superOkDays();
								props.toggleDay();
							}}
							disabled={JSON.stringify(days) === JSON.stringify(shadowCols)}
						>
							Save Changes
						</Button>
					</div>
				</Modal.Footer>
			</Modal>
		</>
	);
}

const mapState = (state) => {
	return {
		dayOpen: state.pickerReducer.dayOpen,
		undoDayState: state.pickerReducer.oldDay.selectDays,
		undoDayStated: state.pickerReducer.oldDay.selectedDays,
		selectDays: state.pickerReducer.fetchSelectDays,
		selectedDays: state.pickerReducer.fetchSelectedDays,
		shadowCols: state.pickerReducer.shadowCols,
		fetchSelectDays: state.pickerReducer.fetchSelectDays,
		fetchSelectedDays: state.pickerReducer.fetchSelectedDays,
	};
};

const mapAction = (dispatch) => {
	return {
		superOkDays: () => dispatch(superOkDays()),
		toggleDay: () => dispatch(toggleDay()),
		undoDays: () => dispatch(undoDays()),
		clearDateHistory: () => dispatch(clearDateHistory()),
		cancelDays: () => dispatch(cancelDays()),
		saveDays: () => dispatch(saveDays()),
	};
};

export default connect(mapState, mapAction)(index);
