/** @format */

import React from "react";
import { connect } from "react-redux";
import {
	openBranchModal,
	openUserModal,
	addSelectTemperarly,
	addSelectedTemperarly,
	addBranchToList,
	addBranchesToList,
	deleteBranchFromList,
} from "../../../Redux/actions/index";
import styled from "styled-components";

const FilterElement = styled.div`
	padding: 0 5px;
	display: flex;
	font-size: 13px;
	line-height: 14px;
	box-shadow: "0 0 10px #333";
	cursor: pointer;
	background-color: ${(props) => {
		const { name: branchName } = props;
		if (branchName == "branch-select") {
			return props.selectbranches.includes(props.item) ? "#4b7ab1" : "";
		} else {
			return props.selectedbranches.includes(props.item) ? "#4b7ab1" : "";
		}
	}};
	color: ${(props) => {
		const { name: branchName } = props;
		if (branchName == "branch-select") {
			return props.selectbranches.includes(props.item) ? "#fff" : "333";
		} else {
			return props.selectedbranches.includes(props.item) ? "#fff" : "333";
		}
	}};
	&:hover {
		background-color: #4b7ab1;
	}
`;

class ModalBranchFilterItem extends React.Component {
	constructor(props) {
		super(props);
		this.clickCount = 0;
		this.singleClickTimer = "";
	}
	singleClick = (keyShift, keyCntrl) => {
		if (this.props.name == "branch-select") {
			if (keyShift) {
				this.props.addSelectTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addSelectTemperarly(this.props.item, false, true);
			} else {
				this.props.addSelectTemperarly(this.props.item, false, false);
			}
		} else {
			if (keyShift) {
				this.props.addSelectedTemperarly(this.props.item, true, false);
			} else if (keyCntrl) {
				this.props.addSelectedTemperarly(this.props.item, false, true);
			} else {
				this.props.addSelectedTemperarly(this.props.item, false, false);
			}
		}
	};
	handleDoubleClick = () => {
		if (this.props.name == "branch-select") {
			this.props.addBranchToList(this.props.item);
		} else {
			this.props.deleteBranchFromList(this.props.item);
		}
	};
	handleClicks(keyShift, keyCntrl) {
		this.clickCount++;
		if (this.clickCount === 1) {
			this.singleClickTimer = setTimeout(
				function() {
					this.clickCount = 0;
					this.singleClick(keyShift, keyCntrl);
				}.bind(this),
				300,
			);
		} else if (this.clickCount === 2) {
			clearTimeout(this.singleClickTimer);
			this.clickCount = 0;
			this.handleDoubleClick();
		}
	}
	render() {
		const { branches, name: branchName, item } = this.props;
		return (
			<FilterElement
				{...this.props}
				onClick={(e) => {
					if (navigator.appVersion.indexOf("Win") != -1) {
						this.handleClicks(e.shiftKey, e.ctrlKey);
					} else {
						this.handleClicks(e.shiftKey, e.metaKey);
					}
				}}
			>
				<p>{branches[item].name}</p>
			</FilterElement>
		);
	}
}

const mapState = (state) => {
	return {
		branches: state.branchesReducer.allBranchs,
		selectbranches: state.branchesReducer.temperarlyBranches.selectBranches,
		selectedbranches: state.branchesReducer.temperarlyBranches.selectedBranches,
	};
};

const mapDispath = (dispatch) => {
	return {
		addBranchToList: (branchId) => dispatch(addBranchToList(branchId)),
		deleteBranchFromList: (branchId) =>
			dispatch(deleteBranchFromList(branchId)),
		addBranchesToList: () => dispatch(addBranchesToList()),
		addSelectTemperarly: (name, isShift, isCntrl) =>
			dispatch(addSelectTemperarly(name, isShift, isCntrl)),
		addSelectedTemperarly: (name, isShift, isCntrl) =>
			dispatch(addSelectedTemperarly(name, isShift, isCntrl)),
		openBranchModal: () => dispatch(openBranchModal()),
		openUserModal: () => dispatch(openUserModal()),
	};
};

export default connect(mapState, mapDispath)(ModalBranchFilterItem);
