import { BASE_URL } from "../../utils/constants";

const addLoader = () => {
	return {
		type: "ADD_LOADER",
	};
};

const countMask = ["first", "second", "third", "fourth", "fifth"];

function getTableData(fetchBody) {
	return function(dispatch) {
		fetch(BASE_URL + "getReport1/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.text())
			.then((response) => {
				dispatch(setTableDate(response));
			});
		fetch(BASE_URL + "getReport2/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.json())
			.then((response) => {
				dispatch(setCSV(response));
			});
	};
}

const setTableDate = (date) => {
	return {
		type: "ADD_TABEL_DATA",
		date,
	};
};

const setCSV = (csv) => {
	return {
		type: "ADD_CSV",
		csv,
	};
};

