let days = [];
for (let i = 1; i < 32; i++) {
	days.push(i);
}

const sortFf = (a, b) => a - b;

const initialState = {
	dayOpen: false,
	selectDays: [...days],
	selectedDays: [],
	oldDay: {
		selectDays: [],
		selectedDays: [],
	},
	temperarlyDays: {
		selectDays: [],
		selectedDays: [],
	},
	shadowCols: {
		selectDays: [],
		selectedDays: [],
	},
	fetchSelectDays: [],
	fetchSelectedDays: [],
};

export default function(state = initialState, action) {
	switch (action.type) {
		case "ADD_EDITOR_DAYS": {
			let selectDays = state.selectDays.filter(
				(item) => !action.days.includes(item),
			);
			console.log("selectDays", selectDays);
			console.log("action.days", action.days);
			return {
				...state,
				selectDays: [...selectDays],
				selectedDays: [...action.days],
				fetchSelectDays: [...selectDays],
				fetchSelectedDays: [...action.days],
			};
		}
		case "TOGGLE_DAY": {
			return {
				...state,
				dayOpen: !state.dayOpen,
				shadowCols: {
					selectDays: [...state.selectDays],
					selectedDays: [...state.selectedDays],
				},
			};
		}
		case "SELECT_DAYS": {
			const SELECTED_DAY = parseInt(action.day);
			const INDEX = state.selectDays.indexOf(SELECTED_DAY);
			let OLD_DAYS = [...state.selectDays];
			let OLD_SELECTED_DAYS = [...state.selectedDays];
			OLD_DAYS.splice(INDEX, 1);
			OLD_SELECTED_DAYS.splice(0, 0, SELECTED_DAY);
			return {
				...state,
				selectDays: OLD_DAYS.sort(sortFf),
				selectedDays: OLD_SELECTED_DAYS.sort(sortFf),
				oldDay: {
					selectDays: [...state.selectDays],
					selectedDays: [...state.selectedDays],
				},
			};
		}
		case "DELETE_DAYS": {
			const SELECTED_DAY = parseInt(action.day);
			const INDEX = state.selectedDays.indexOf(SELECTED_DAY);
			let OLD_DAYS = [...state.selectedDays];
			let OLD_SELECTED_DAYS = [...state.selectDays];
			OLD_DAYS.splice(INDEX, 1);
			OLD_SELECTED_DAYS.splice(0, 0, SELECTED_DAY);
			return {
				...state,
				selectDays: OLD_SELECTED_DAYS.sort(sortFf),
				selectedDays: OLD_DAYS.sort(sortFf),
				oldDay: {
					selectDays: [...state.selectDays],
					selectedDays: [...state.selectedDays],
				},
			};
		}
		case "SELECT_ALL_DAYS": {
			const ALL_DAYS = [...state.selectDays, ...state.selectedDays];
			if (state.selectDays.length == 0) {
				return {
					...state,
				};
			}
			return {
				...state,
				selectDays: [],
				selectedDays: [...ALL_DAYS].sort(sortFf),
				oldDay: {
					selectDays: [...state.selectDays],
					selectedDays: [...state.selectedDays],
				},
				temperarlyDays: {
					selectDays: [],
					selectedDays: [],
				},
			};
		}
		case "DELETE_ALL_DAYS": {
			const ALL_DAYS = [...state.selectDays, ...state.selectedDays];
			if (state.selectedDays.length == 0) {
				return {
					...state,
				};
			}
			return {
				...state,
				selectDays: [...ALL_DAYS].sort(sortFf),
				selectedDays: [],
				oldDay: {
					selectDays: [...state.selectDays],
					selectedDays: [...state.selectedDays],
				},
				temperarlyDays: {
					selectDays: [],
					selectedDays: [],
				},
			};
		}
		case "UNDO_DAYS": {
			return {
				...state,
				selectDays: [...state.oldDay.selectDays].sort(sortFf),
				selectedDays: [...state.oldDay.selectedDays].sort(sortFf),
				oldDay: {
					selectDays: [],
					selectedDays: [],
				},
			};
		}
		case "DAY_OK": {
			return {
				...state,
				oldDay: {
					selectDays: [],
					selectedDays: [],
				},
			};
		}
		case "ADD_DAYS_TOLIST": {
			const days = state.temperarlyDays.selectDays;
			let oldSelectDays = [...state.selectDays],
				oldSelectedDays = [...state.selectedDays];
			days.forEach((item) => {
				let index = oldSelectDays.indexOf(item);
				oldSelectDays.splice(index, 1);
				oldSelectedDays.push(item);
			});
			return {
				...state,
				selectDays: oldSelectDays.sort(sortFf),
				selectedDays: oldSelectedDays.sort(sortFf),
				temperarlyDays: {
					...state.temperarlyDays,
					selectDays: [],
				},
				oldDay: {
					selectDays: [...state.selectDays],
					selectedDays: [...state.selectedDays],
				},
			};
		}
		case "DELETE_DAYS_FROMLIST": {
			const days = state.temperarlyDays.selectedDays;
			let oldSelectDays = [...state.selectDays],
				oldSelectedDays = [...state.selectedDays];
			days.forEach((item) => {
				let index = oldSelectedDays.indexOf(item);
				oldSelectedDays.splice(index, 1);
				oldSelectDays.push(item);
			});
			return {
				...state,
				selectDays: oldSelectDays.sort(sortFf),
				selectedDays: oldSelectedDays.sort(sortFf),
				temperarlyDays: {
					...state.temperarlyDays,
					selectedDays: [],
				},
				oldDay: {
					selectDays: [...state.selectDays],
					selectedDays: [...state.selectedDays],
				},
			};
		}
		case "ADD_SELECT_DAYS_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			let isHas = state.temperarlyDays.selectDays.includes(name);
			let newArr = [...state.temperarlyDays.selectDays];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}
			return {
				...state,
				temperarlyDays: {
					...state.temperarlyDays,
					selectDays: newArr,
				},
			};
		}
		case "ADD_SELECTED_DAYS_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			console.log("isCntrl", isCntrl);
			let isHas = state.temperarlyDays.selectDays.includes(name);
			let newArr = [...state.temperarlyDays.selectedDays];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}
			return {
				...state,
				temperarlyDays: {
					...state.temperarlyDays,
					selectedDays: newArr,
				},
			};
		}
		case "CLEAR_DATE_HISTORY": {
			return {
				...state,
				oldDay: {
					selectDays: [],
					selectedDays: [],
				},
			};
		}
		case "CANCEL_DAYS": {
			return {
				...state,
				selectDays: [...state.shadowCols.selectDays],
				selectedDays: [...state.shadowCols.selectedDays],
			};
		}
		case "CLEAR_DAY": {
			return {
				...state,
				selectDays: [...state.selectDays, ...state.selectedDays],
				selectedDays: [],
			};
		}
		case "SAVE_DAYS": {
			return {
				...state,
				fetchSelectDays: [...state.selectDays],
				fetchSelectedDays: [...state.selectedDays],
			};
		}
		default:
			return {
				...state,
			};
	}
}
