/** @format */

const startYear = 2020;
const currYear = new Date().getFullYear();
const diffYear = currYear - startYear + 1;
let years = [];
for (let i = 0; i < diffYear; i++) {
	years.push(startYear + i);
}

const sortFf = (a, b) => a - b;

const initialState = {
	yearOpen: false,
	selectYears: [...years],
	selectedYears: [],
	oldYear: {
		selectYears: [],
		selectedYears: [],
	},
	temperarlyYears: {
		selectYears: [],
		selectedYears: [],
	},
	shadowCols: {
		selectYears: [],
		selectedYears: [],
	},
	fetchSelectYears: [],
	fetchSelectedYears: [],
};

export default function(state = initialState, action) {
	switch (action.type) {
		case "ADD_EDITOR_YEARS": {
			let selectYears = state.selectYears.filter(
				(item) => !action.years.includes(item),
			);
			console.log("selectYears", selectYears);
			console.log("action.years", action.years);
			return {
				...state,
				selectYears: [...selectYears],
				selectedYears: [...action.years],
				fetchSelectYears: [...selectYears],
				fetchSelectedYears: [...action.years],
			};
		}
		case "TOGGLE_YEAR": {
			return {
				...state,
				yearOpen: !state.yearOpen,
				shadowCols: {
					selectYears: [...state.selectYears],
					selectedYears: [...state.selectedYears],
				},
			};
		}
		case "SELECT_YEAR": {
			const SELECTED_YEAR = action.year;
			const INDEX = state.selectYears.indexOf(SELECTED_YEAR);
			let OLD_YEAR = [...state.selectYears];
			let OLD_SELECTED_YEAR = [...state.selectedYears];
			OLD_YEAR.splice(INDEX, 1);
			OLD_SELECTED_YEAR.splice(0, 0, SELECTED_YEAR);
			return {
				...state,
				selectYears: [...OLD_YEAR],
				selectedYears: [...OLD_SELECTED_YEAR],
				oldYear: {
					selectYears: [...state.selectYears],
					selectedYears: [...state.selectedYears],
				},
			};
		}
		case "DELETE_YEAR": {
			const SELECTED_YEAR = action.year;
			const INDEX = state.selectedYears.indexOf(SELECTED_YEAR);
			let OLD_YEAR = [...state.selectedYears];
			let OLD_SELECT_YEAR = [...state.selectYears];
			OLD_YEAR.splice(INDEX, 1);
			OLD_SELECT_YEAR.splice(0, 0, SELECTED_YEAR);
			return {
				...state,
				selectYears: OLD_SELECT_YEAR.sort(sortFf),
				selectedYears: OLD_YEAR.sort(sortFf),
				oldYear: {
					selectYears: [...state.selectYears],
					selectedYears: [...state.selectedYears],
				},
			};
		}
		case "SELECT_ALL_YEAR": {
			const ALL_YEARS = [...state.selectYears, ...state.selectedYears];
			if (state.selectYears.length == 0) {
				return {
					...state,
				};
			}
			return {
				...state,
				selectYears: [],
				selectedYears: [...ALL_YEARS].sort(sortFf),
				oldYear: {
					selectYears: [...state.selectYears],
					selectedYears: [...state.selectedYears],
				},
			};
		}
		case "DELETE_ALL_YEAR": {
			const ALL_YEARS = [...state.selectYears, ...state.selectedYears];
			if (state.selectedYears.length == 0) {
				return {
					...state,
				};
			}
			return {
				...state,
				selectYears: [...ALL_YEARS].sort(sortFf),
				selectedYears: [],
				oldYear: {
					selectYears: [...state.selectYears],
					selectedYears: [...state.selectedYears],
				},
			};
		}
		case "UNDO_YEAR": {
			return {
				...state,
				selectYears: [...state.oldYear.selectYears].sort(sortFf),
				selectedYears: [...state.oldYear.selectedYears].sort(sortFf),
				oldYear: {
					selectYears: [],
					selectedYears: [],
				},
			};
		}
		case "OK_YEAR": {
			return {
				...state,
				oldYear: {
					selectYears: [],
					selectedYears: [],
				},
			};
		}
		case "ADD_YEARS_TOLIST": {
			const yearsSelect = state.temperarlyYears.selectYears;
			let oldSelectYears = [],
				oldSelectedYear = [];
			for (let [key, value] of Object.entries(state.selectYears)) {
				oldSelectYears.push(value);
			}
			for (let [key, value] of Object.entries(state.selectedYears)) {
				oldSelectedYear.push(value);
			}
			yearsSelect.forEach((item) => {
				let index = oldSelectYears.indexOf(item);
				oldSelectYears.splice(index, 1);
				oldSelectedYear.push(item);
			});
			return {
				...state,
				selectYears: [...oldSelectYears].sort(sortFf),
				selectedYears: [...oldSelectedYear].sort(sortFf),
				temperarlyYears: {
					...state.temperarlyYears,
					selectYears: [],
				},
				oldYear: {
					selectYears: [...state.selectYears],
					selectedYears: [...state.selectedYears],
				},
			};
		}
		case "DELETE_YEARS_FROMLIST": {
			const year = state.temperarlyYears.selectedYears;
			let oldSelectYears = [...state.selectYears],
				oldSelectedYears = [...state.selectedYears];
			year.forEach((item) => {
				let index = oldSelectYears.indexOf(item);
				oldSelectedYears.splice(index, 1);
				oldSelectYears.push(item);
			});
			return {
				...state,
				selectYears: oldSelectYears.sort(sortFf),
				selectedYears: oldSelectedYears.sort(sortFf),
				temperarlyYears: {
					...state.temperarlyYears,
					selectedYears: [],
				},
				oldYear: {
					selectYears: [...state.selectYears],
					selectedYears: [...state.selectedYears],
				},
			};
		}
		case "ADD_SELECT_YEARS_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			let isHas = state.temperarlyYears.selectYears.includes(name);
			let newArr = [...state.temperarlyYears.selectedYears];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}
			return {
				...state,
				temperarlyYears: {
					...state.temperarlyYears,
					selectYears: newArr,
				},
			};
		}
		case "ADD_SELECTED_YEARS_TEMPERARLY": {
			const { name, isShift, isCntrl } = action;
			let isHas = state.temperarlyYears.selectYears.includes(name);
			let newArr = [...state.temperarlyYears.selectedYears];
			if (isHas) {
				let index = newArr.indexOf(name);
				newArr.splice(index, 1);
			} else {
				if (isCntrl) {
					newArr = [...newArr, name];
				} else {
					newArr = [name];
				}
			}
			return {
				...state,
				temperarlyYears: {
					...state.temperarlyYears,
					selectedYears: newArr,
				},
			};
		}
		case "CANCEL_YEARS": {
			return {
				...state,
				selectYears: [...state.shadowCols.selectYears],
				selectedYears: [...state.shadowCols.selectedYears],
			};
		}
		case "CLEAR_YEAR": {
			return {
				...state,
				selectYears: [...state.selectYears, ...state.selectedYears],
				selectedYears: [],
			};
		}
		case "SAVE_YEARS": {
			return {
				...state,
				fetchSelectYears: [...state.selectYears],
				fetchSelectedYears: [...state.selectedYears],
			};
		}
		default:
			return { ...state };
	}
}
