import React, { useState } from "react";
import { Table, Form } from "react-bootstrap";
import styled from "styled-components";
import ListItem from "./ListItem";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

function Alert(props) {
	return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const TableWrap = styled(Table)`
	& td {
		text-align: left;
		vertical-align: baseline;
		text-transform: none !important;
	}
	& th {
		text-align: left;
		vertical-align: baseline;
	}
	& th:last-child {
		text-align: center;
	}
`;

export default function List({
	reports,
	handleSetEditable,
	handleClose,
	handleOpen,
	open,
	openError,
	handleOpenError,
	handleCloseError,
}) {
	const [search, setSearch] = useState("");

	const handleChangeSearch = (e) => setSearch(e.target.value);
	return (
		<div>
			<div>
				<Snackbar
					open={openError}
					autoHideDuration={5000}
					onClose={handleCloseError}
				>
					<Alert onClose={handleCloseError} severity="error">
						Name already in use
					</Alert>
				</Snackbar>
				<Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
					<Alert onClose={handleClose} severity="success">
						Success!
					</Alert>
				</Snackbar>
			</div>
			<div>
				<Form.Control
					className="search"
					type="search"
					placeholder="Type to search"
					style={{
						width: "290px",
						float: "right",
						marginBottom: "6px",
					}}
					value={search}
					onChange={handleChangeSearch}
				/>
			</div>
			{reports.length < 1 ? null : (
				<TableWrap className="report-list" striped bordered hover>
					<thead>
						<tr>
							<th
								style={{
									width: "3%",
								}}
							>
								#
							</th>
							<th>Name</th>
							<th
								style={{
									width: "10%",
								}}
							>
								Actions
							</th>
						</tr>
					</thead>
					<tbody>
						{reports
							.filter((item) => {
								let lowerText = search.toLowerCase(),
									lowerItem = item.name.toLowerCase();
								var patt = new RegExp(lowerText);
								var res = patt.test(lowerItem);
								return res;
							})
							.map((item, index) => (
								<ListItem
									key={index}
									item={item}
									index={index}
									handleSetEditable={handleSetEditable}
									handleOpen={handleOpen}
									handleOpenError={handleOpenError}
								/>
							))}
					</tbody>
				</TableWrap>
			)}
		</div>
	);
}
