import { BASE_URL } from "../../utils/constants";

const addLoader = () => {
	return {
		type: "ADD_LOADER",
	};
};

function clearDateHistory() {
	return {
		type: "CLEAR_DATE_HISTORY",
	};
}

function saveYear() {
	return {
		type: "SAVE_YEARS",
	};
}

// table actions

const setTableDate = (date) => {
	return {
		type: "ADD_TABEL_DATA",
		date,
	};
};

const setCSV = (csv) => {
	return {
		type: "ADD_CSV",
		csv,
	};
};

function getTableData(fetchBody) {
	return function(dispatch) {
		fetch(BASE_URL + "getReport1/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.text())
			.then((response) => {
				dispatch(setTableDate(response));
			});
		fetch(BASE_URL + "getReport2/", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify(fetchBody),
		})
			.then((response) => response.json())
			.then((response) => {
				dispatch(setCSV(response));
			});
	};
}

