import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { BASE_URL } from "../../utils/constants";
import { connect } from "react-redux";
import { parseBody } from "../../utils/bodyReverce";
import { superAddFiteredItems } from "../../Redux/actions/";
function ListItem({
	index,
	item,
	handleSetEditable,
	handleOpen,
	handleOpenError,
	superAddFiteredItems,
}) {
	const [name, setName] = useState("");
	useEffect(() => console.log("name", name), [name]);

	const handleUpdateName = async (id, name) => {
		const response = await fetch(BASE_URL + "updateName", {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify({
				id: id,
				name: name,
			}),
		});
		const data = await response.text();
		if (data == 1) {
			handleSetEditable(item.id);
			handleOpen();
		} else {
			handleOpenError();
		}
		console.log("handleUpdateName -> data", data);
		parseBody(data);
	};

	const handleSetName = (e) => setName(e.target.value);

	const handleSetEditableItem = (id, name) => {
		setName(name);
		handleSetEditable(item.id);
	};

	const handleEdit = async (name, id) => {
		const response = await fetch(BASE_URL + "getReportList", {
			method: "POST",
			headers: {
				"Content-type": "application/json",
				Accept: "application/json",
			},
			body: JSON.stringify({
				name: name,
			}),
		});
		const data = await response.json();
		const parsed = JSON.parse(data[0].jsonContent);
		console.log("handleEdit -> parsed", parsed);
		let items = parseBody(parsed);
		superAddFiteredItems(items, name, id);
	};

	return (
		<tr>
			<td>{index + 1}</td>
			<td>
				{item.isEditable ? (
					<div className="d-flex">
						<input
							style={{
								borderRadius: "3px",
								border: "1px solid #c7c1c1",
								paddingLeft: "5px",
							}}
							type="text"
							defaultValue={item.name}
							onChange={handleSetName}
						/>
						<Button
							onClick={() => {
								handleUpdateName(item.id, name);
							}}
							variant="success"
						>
							Save
						</Button>
					</div>
				) : (
					<div
						style={{
							cursor: "pointer",
						}}
						onClick={() => handleEdit(item.name, item.id)}
					>
						{item.name}
					</div>
				)}
			</td>
			{
				<td>
					<div
						style={{
							display: "flex",
						}}
					>
						<Button
							variant="warning"
							className="mr-2"
							onClick={() => handleEdit(item.name, item.id)}
						>
							Edit
						</Button>{" "}
						<Button
							variant="light"
							onClick={() => handleSetEditableItem(item.id, item.name)}
						>
							Rename
						</Button>
					</div>
				</td>
			}
		</tr>
	);
}

const mapDispatch = (dispatch) => {
	return {
		superAddFiteredItems: (items, name, id) =>
			dispatch(superAddFiteredItems(items, name, id)),
	};
};

export default connect(null, mapDispatch)(ListItem);
