/** @format */

import React from "react";
import ModalUserFilterItem from "./ModalUserFilteritem";
import { connect } from "react-redux";
import {
	addYearSelectedTemperarly,
	addYearSelectTemperarly,
} from "../../../Redux/actions";

function UserColumnItem(props) {
	const { name } = props;
	return (
		<div
			className="filtder"
			style={{
				minHeight: "180px",
			}}
		>
			{props.itemList.length != 0 ? (
				props.itemList.map((item, index) => {
					return <ModalUserFilterItem key={item} colname={name} name={item} />;
				})
			) : (
				<p></p>
			)}
		</div>
	);
}

const mapState = (state) => {
	return {
		users: state.usersReducer.allUsers,
	};
};

export default connect(mapState)(UserColumnItem);
