import store from "../Redux/store";
const countMask = ["first", "second", "third", "fourth", "fifth"];
const countMaskWidthoutParams = [
	"freeCount",
	"maxFreeTime",
	"totalFreeTime",
	"totalSessionTime",
];
const filtersElements = [
	"branches",
	"year",
	"month",
	"users",
	"day",
	"count",
	"total_session_time",
	"max_free_time",
	"total_free_time",
];

const trueObj = {
	branchName: "branches",
	year: "year",
	day: "day",
	month: "month",
	fullname: "users",
	freeCount: "count",
	maxFreeTime: "max_free_time",
	totalFreeTime: "total_free_time",
	totalSessionTime: "total_session_time",
};

function parseBody(data) {
	let filterparams = [];
	let params = {};
	let widthparamObject = {};
	let widthOutParamObject = {};
	Object.keys(data).forEach((item) => {
		let isContain = countMask.includes(item);
		let isCountainWidthout = countMaskWidthoutParams.includes(item);
		if (isContain) {
			widthparamObject[item] = data[item];
		} else if (isCountainWidthout) {
			widthOutParamObject[item] = data[item];
		} else {
			params[item] = data[item];
		}
	});
	let widthOutParamObjectKeys = Object.keys(widthOutParamObject);
	let needObj = {};

	let withparamsvalues = Object.values(widthparamObject);
	withparamsvalues.forEach((item) => {
		needObj = {
			...needObj,
			...item,
		};
	});
	let needKeys = Object.keys(needObj);
	needKeys.forEach((item) => {
		filterparams.push(trueObj[item]);
	});
	widthOutParamObjectKeys.forEach((item) => {
		filterparams.push(trueObj[item]);
	});
	return [filterparams, params];
}
export { parseBody };
