/** @format */

import React from "react";
import { connect } from "react-redux";
import DaySelect from "./DaySelect";
import DaySelected from "./DaySelected";
import {
	addDaysToSelect,
	deleteDaysToSelect,
	selectAllDays,
	deleteAllDays,
} from "../../../../Redux/actions";
import DaArrow from "./DaArrow";

function Month(props) {
	var colLeft = props.selectDays;
	var colRighr = props.selectedDays;

	return (
		<div className="modal-body">
			<DaySelect branches={props.branches} list={colLeft} itemList={colLeft} />
			<DaArrow />
			<DaySelected
				branches={props.branches}
				list={colRighr}
				itemList={colRighr}
			/>
		</div>
	);
}

const mapState = (state) => {
	return {
		selectDays: state.pickerReducer.selectDays,
		selectedDays: state.pickerReducer.selectedDays,
	};
};

const mapDispatch = (dispatch) => {
	return {
		selectAllDays: () => dispatch(selectAllDays()),
		deleteAllDays: () => dispatch(deleteAllDays()),
		addDaysToSelect: (day) => dispatch(addDaysToSelect(day)),
		deleteDaysToSelect: (day) => dispatch(deleteDaysToSelect(day)),
	};
};

export default connect(mapState, mapDispatch)(Month);
