/** @format */

import React from "react";
import UserColumn from "./UserColumn";
import TextField from "@material-ui/core/TextField";
import { connect } from "react-redux";
import { userSelectSearch } from "../../../Redux/actions";

function Select(props) {
	return (
		<div className="modal-body-select">
			<TextField
				InputProps={{
					className: props.classes.input,
				}}
				InputLabelProps={{
					classes: {
						root: props.classes.labelRoot,
					},
				}}
				id="outlined-basic"
				label="Search"
				variant="outlined"
				onInput={(e) => props.userSelectSearch(e.target.value)}
			/>
			<div className="modal-filter modal-filter-select">
				{
					<UserColumn
						name="user-select"
						itemList={props.colLeft.filter((item) => {
							let lowerText = props.text.toLowerCase(),
								lowerItem = item.toLowerCase();
							var patt = new RegExp(lowerText);
							var res = patt.test(lowerItem);
							return res;
						})}
					/>
				}
			</div>
		</div>
	);
}

const mapState = (state) => {
	return {
		text: state.usersReducer.selectSearchText,
	};
};

const mapDispatch = (dispatch) => {
	return {
		userSelectSearch: (text) => dispatch(userSelectSearch(text)),
	};
};

export default connect(mapState, mapDispatch)(Select);
